define('views/MainScreen',[
    'framework/Screen',
    'framework/Container',
    'views/SearchView',
    'views/MainScreenWidgetView',
    'views/CategoryListView',
    'views/CategoryView',
    'views/SubcategoryView',
    'views/ProductView',
    'views/TicketView',
    'views/TicketBuyView',
    'views/TicketConfirmView',
    'views/TicketSuccessView',
    'views/SearchResultsView',
    'models/Category',
    'models/Subcategory',
    'models/Product',
    'templates/main-screen',
    'framework/utils/log',

    'views/HotelCategoriesView',
    'views/HotelsView',
    'views/DosugView',
    'views/TheaterView',
    'views/CultureView',
    'views/TheaterEventsView',
    'views/HisMuseumView',
    'views/HotelView',
    'views/TransportView',
    'views/ShopsView',
    'views/FoodView',
    'views/ReastoranView',
    'views/CafeView',
    'views/StolovayaView',
    'views/BarView',
    'views/SamohvalView',
    'views/NightClubView',
    'views/BuolingView',
    'views/CinemaView',
    'views/StadionView',
    'views/ParkView',
    'views/HudMuseumView',
    'views/TechMuseumView',
    'views/KraevMuseumView',
    'views/CultMuseumView',
    'views/HostelView',
    'views/GuestHousView',
    'views/ApartmentView',
    'views/RestBaseView',
], function (Screen, Container, SearchView, MainScreenWidgetView, CategoryListView, CategoryView, SubcategoryView, ProductView, TicketView, TicketBuyView, TicketConfirmView, TicketSuccessView, SearchResultsView, Category, Subcategory, Product, template, log, HotelCategoriesView, HotelsView, DosugView, TheaterView, CultureView, TheaterEventsView, HisMuseumView, HotelView, TransportView, ShopsView, FoodView, ReastoranView, CafeView, StolovayaView, BarView, SamohvalView, NightClubView, BuolingView, CinemaView, StadionView, ParkView, HudMuseumView, TechMuseumView, KraevMuseumView, CultMuseumView, HostelView, GuestHousView, ApartmentView, RestBaseView) {


    'use strict';

    return Screen.extend({
        className : 'main-screen',
        template : template,
        routes : {
            '' : 'categories',
            'categories/:id' : 'category',
            'subcategories/:id(/:page)' : 'subcategory',
            'products/:id' : 'product',
            'search/:query(/:page)' : 'search',
            'ticket': 'ticket',
            'confirmTicket': 'confirmTicket',
            'buyTicket': 'buyTicket',
            'successTicket': 'successTicket',
            'hotel-categories' : 'hotelCategories',
            'hotels(/:page)' : 'hotels',
            'dosug' : 'dosug',
            'theater' : 'theater',
            'culture' : 'culture',
            'theater-events(/:page)' : 'theaterEvents',
            'museum': 'showMuseum',
            'hotel': 'showHotel',
            'transport': 'transport',
            'shops': 'shops',
            'food': 'food',
            'reatoran': 'reatoran',
            'cafe': 'cafe',
            'stolovaya': 'stolovaya',
            'bar': 'bar',
            'samohval': 'samohval',
            'club': 'club',
            'bouling': 'bouling',
            'kino': 'kino',
            'stadion': 'stadion',
            'park': 'park',
            'hud' : 'hud',
            'tech' : 'tech',
            'kraev' : 'kraev',
            'culcenter': 'culcenter',
            'hostel': 'hostel',
            'guest': 'guest',
            'apart': 'apart',
            'restbase' : 'restbase'
        },
        regions : {
            search : '.main-screen-search',
            content : '.main-screen-content',
            footer : '.main-screen-footer'
        },
        subviews : {
            container : Container,
            searchView: SearchView,
            widgetView : MainScreenWidgetView
        },
        layout : {
            search : 'searchView',
            content : 'container',
            footer : 'widgetView'
        },
        hotelCategories : function () {
            this.init();

            this.subviews.container.show(new HotelCategoriesView);

            this.subviews.widgetView.select('map');
            Firefly.trigger('map:locations:product', HotelsView.prototype.data().map(function (d) { return d.locations[0]; }));

        },
        dosug : function () {
             this.init();

            this.subviews.container.show(new DosugView);
        },
        culture : function () {
            this.init();

            this.subviews.container.show(new CultureView);
        },
        theater : function () {
            this.init();

            this.subviews.container.show(new TheaterView);
        },
        transport : function () {
            this.init();

            this.subviews.container.show(new TransportView);

        },
        shops : function () {
            this.init();

            this.subviews.container.show(new ShopsView);

        },
        food : function () {
            this.init();

            this.subviews.container.show(new FoodView);

        },
        reatoran : function () {
            this.init();

            this.subviews.container.show(new ReastoranView);

        },
        cafe : function () {
            this.init();

            this.subviews.container.show(new CafeView);

        },
        stolovaya : function () {
            this.init();

            this.subviews.container.show(new StolovayaView);

        },
        bar : function () {
            this.init();

            this.subviews.container.show(new BarView);

        },
        samohval : function () {
            this.init();

            this.subviews.container.show(new SamohvalView);

        },
        club : function () {
            this.init();

            this.subviews.container.show(new NightClubView);

        },
        bouling : function () {
            this.init();

            this.subviews.container.show(new BuolingView);

        },
        kino : function () {
            this.init();

            this.subviews.container.show(new CinemaView);

        },
        stadion : function () {
            this.init();

            this.subviews.container.show(new StadionView);

        },
        park : function () {
            this.init();

            this.subviews.container.show(new ParkView);

        },
        hud : function () {
            this.init();

            this.subviews.container.show(new HudMuseumView);

        },
        tech : function () {
            this.init();

            this.subviews.container.show(new TechMuseumView);

        },
        kraev : function () {
            this.init();

            this.subviews.container.show(new KraevMuseumView);

        },
        culcenter : function () {
            this.init();

            this.subviews.container.show(new CultMuseumView);

        },
        hostel : function () {
            this.init();

            this.subviews.container.show(new HostelView);

        },
        guest : function () {
            this.init();

            this.subviews.container.show(new GuestHousView);

        },
        apart : function () {
            this.init();

            this.subviews.container.show(new ApartmentView);

        },
        restbase : function () {
            this.init();

            this.subviews.container.show(new RestBaseView);

        },
        hotels : function (page) {
            this.init();


            var currentView = this.subviews.container.currentView;

            page = page ? parseInt(page, 10) : 0;


            if (currentView && (currentView instanceof HotelsView)) {
                currentView.goToPage(page);
                return;
            }

            this.subviews.container.show(new HotelsView({
                page : page
            }));

        },
        theaterEvents : function (page) {
            this.init();


            var currentView = this.subviews.container.currentView;

            page = page ? parseInt(page, 10) : 0;


            if (currentView && (currentView instanceof TheaterEventsView)) {
                currentView.goToPage(page);
                return;
            }

            this.subviews.container.show(new TheaterEventsView({
                page : page
            }));

        },
        init : function () {
            if (this.inited) {
                return;
            }

            this.inited = true;

            Firefly.viewport.hideModal();
        },
        categories: function () {
            this.init();

            this.subviews.searchView.clear();
            this.subviews.searchView.show();

            this.subviews.container.show(new CategoryListView());
            this.subviews.widgetView.select('map');
            window.Firefly.trigger('map:locations:clear');
        },
        category : function (id) {
            this.init();

            this.subviews.searchView.show();

            var category = new Category({
                id: parseInt(id, 10)
            }),

            categoryView = new CategoryView({
                model: category
            });

            this.subviews.container.show(categoryView);
            this.subviews.widgetView.select('banner');
            window.Firefly.trigger('map:locations:clear');
        },
        subcategory : function (id, page) {
            this.init();

            this.subviews.searchView.show();


            var currentView = this.subviews.container.currentView,
                subcategory;

            id = parseInt(id, 10);
            page = page ? parseInt(page, 10) : 0;


            if (currentView && (currentView instanceof SubcategoryView) && currentView.model.id === id) {
                currentView.goToPage(page);
                return;
            }

            subcategory = new Subcategory({ id : id });

            this.subviews.container.show(new SubcategoryView({
                model: subcategory,
                page: page
            }));

            this.subviews.widgetView.select('map');
        },
        product : function (id) {
            this.init();

            this.subviews.searchView.show();

            var product = new Product({
                id: parseInt(id, 10)
            }),

            productView = new ProductView({
                model: product
            });

            this.subviews.container.show(productView);
            this.subviews.widgetView.select('map');
        },
        search : function (query, page) {
            this.init();
            this.subviews.searchView.show(); // TODO move somwhere

            var currentView = this.subviews.container.currentView;

            page = page ? parseInt(page, 10) : 0;

            if (currentView && (currentView instanceof SearchResultsView) && currentView._query === query) {
                currentView.goToPage(page);
                return;
            }

            log.event('search:word', {
                query: query
            });

            this.subviews.container.show(new SearchResultsView({
                query : query,
                page : page
            }));
        },
        ticket : function () {
            this.init();


            this.subviews.container.show(new TicketView);

            this.subviews.widgetView.select('map');
        },
        buyTicket: function () {
            this.init();
            this.subviews.container.show(new TicketBuyView());
            this.subviews.widgetView.select('map');
        },
        confirmTicket: function () {
            this.init();
            this.subviews.container.show(new TicketConfirmView());
            this.subviews.widgetView.select('map');
        },
        successTicket: function () {
            this.init();
            this.subviews.container.show(new TicketSuccessView());
            this.subviews.widgetView.select('map');
        },

        showMuseum: function () {
            this.init();
            console.log(HisMuseumView);
            this.subviews.container.show(new HisMuseumView());
            this.subviews.widgetView.select('map');
        },
        showHotel: function () {
            this.init();
            this.subviews.container.show(new HotelView());
            this.subviews.widgetView.select('map');
        }
    });
});

