define('views/TicketViewTable',[
    'underscore',
    'jquery',
    'backbone',
    'framework/View',
    'templates/ticket-view-table',
    'templates/ticket-view-table-row',
    'models/ticketCart',
    'framework/utils/ticketBackend',
    'views/TicketViewHallScheme',
    'views/MessageView',
    'framework/utils/Scroll'
], function (_, $,  Backbone, View, template, rowTemplate, ticketCart, ticketBackend, TicketViewHallScheme, MessageView, Scroll) {
    'use strict';
    /*
    Row is a tr which is filled in accordance with the model - ticket
     */
    var activeButton,
        controller = {},
        // Row view
        Row = Backbone.View.extend({
        tagName: 'tr',

        events: {
            'mousedown .ticket-view-table-choice-plus, .ticket-view-table-choice-minus, .ticket-view-table-row-choose': 'toggle',
            'mouseup .ticket-view-table-choice-plus': 'clickPlus',
            'mouseup .ticket-view-table-choice-minus': 'clickMinus',
            'mouseup .ticket-view-table-row-choose': 'showScheme'
        },
        deltaY: 18,
        initialize: function () {

        },
        toggle: function ($evt) {

            if($evt.fromElement) {
                return;
            }

            var that = this;
            this.$button = $($evt.currentTarget);
            controller.activeView = activeButton = this;
            this.toggled = true;



            setTimeout(function () {

                if (that.toggled) {
                    that.$button.addClass('active');
                }

            }, 40);
        },
        rollBack: function () {
            this.toggled = false;
            this.$button.removeClass('active');
        },
        showScheme: function () {
            if (!controller.activeView.toggled) {
                return;
            }
            var self = this,
                amount = ticketCart.getByCategoryId(this.model.get('id')).length,
                newAmount,
                hallScheme = new TicketViewHallScheme({ model : this.model });
            this.listenToOnce(hallScheme, 'changeAmount', function () {
                if (ticketCart.initialized &&
                        (newAmount = ticketCart.getByCategoryId(this.model.get('id')).length) !== amount) {
                    self.$el.find('span').animate({
                        opacity: 0
                    }, {
                        done: function () {
                            var self = this;
                            if (newAmount === 0) {
                                $(self).parent().removeClass('number');
                                $(self).css('color', 'transparent').text('').animate({opacity: 1}, {
                                    done: function () {
                                        $(self).css('color', 'rgb(65, 64, 64)').text('выбрать билет').css('color', 'rgb(65, 64, 64)');
                                    }
                                });
                            } else {
                                $(self).text(newAmount).animate({opacity: 1}).parent().addClass('number');
                            }

                        }
                    }, 1000);
                }

            });
            Firefly.viewport.showModal(hallScheme);
            this.rollBack();
        },
        render: function  () {
            var modelData = {},
                ticket = ticketCart.get(this.model);

            if (this.model.get('seats')) {
                _.extend(modelData, {
                    amount: ticketCart.getByCategoryId(this.model.id).length || 0
                });
            } else if (ticket) {
                _.extend(modelData, {
                    amount: ticket.get('amount')
                });
            }

            _.extend(modelData, this.model.toJSON());
            this.$el.html(rowTemplate(modelData));
            return this;
        },
        remove: function () {
            // hides it with animation and then removes from dom
            this.$el.hide(300, function () { this.remove(); });
        },
        clickMinus: function (evt) {

            if (!evt.fromElement && this.toggled) {
                controller.activeView = this;
                (function (curAmount, $el, dy, that) {
                    if (curAmount !== 0) {
                        var $curEl = $el.find('span[data-active]');

                        if ($curEl.prev().length === 0) {
                            $el.prepend('<span style="margin-top:-18px"> ' + (curAmount - 1));
                        }

                        setTimeout(function () {
                            $el.find('span').first().css('margin', 0);
                        }, 0);

                        $curEl.removeAttr('data-active');
                        $curEl.prev().attr('data-active', curAmount - 1);
                        $el.css('transform', 'translateY(' + ($curEl.prevAll().length - 1) * (-dy) + 'px)');
                        ticketCart.rmTicket(that.model);
                    }
                })(+this.$el.find('span[data-active]').attr('data-active'),
                    this.$el.find('.numbers-container'),
                    this.deltaY, this);
                this.rollBack();
            }

        },
        clickPlus: function (evt) {
            if (!evt.fromElement && this.toggled) {


                this.rollBack();
                controller.activeView = this;
                var _model = ticketCart.get(this.model) || this.model,
                    currentAmount = _model.get('amount') || 0,
                    availableAmount = _model.get('available');
                if (ticketCart.calculateTotalTickets() >= ticketCart.CONSTRAINT) {
                    Firefly.viewport.showMessage(new MessageView({
                        message: 'невозможно выбрать больше ' + ticketCart.CONSTRAINT + ' билетов'
                    }));
                    return;
                }
                if (currentAmount >= availableAmount) {
                    Firefly.viewport.showMessage(new MessageView({
                        message: 'в данной категории доступно только ' + availableAmount + ' билетов'
                    }));
                    return;
                }

                (function (curAmount, $el, dy) {
                    var $curEl = $el.find('span[data-active]');
                    if ($curEl.next().length === 0) {
                        $el.append('<span>' + (curAmount + 1));
                    }
                    $curEl.removeAttr('data-active');
                    $curEl.next().attr('data-active', curAmount + 1);
                    $el.css('transform', 'translateY(' + ($curEl.prevAll().length + 1) * (-dy) + 'px)');
                    if ($curEl.next().length === 0) {
                        $el.append('<span>' + (curAmount + 1));
                    }
                    ticketCart.addTicket(_model);

                })(+this.$el.find('span[data-active]').attr('data-active'),
                    this.$el.find('.numbers-container'),
                    this.deltaY);

            }

        }

    }),

    // Row (ticket) model
    TicketModel = Backbone.Model.extend({
        initialize: function () {
            this.url = this.url() + '/seats';
        }
    }),
    // Collection of rows (tickets)
    TicketCollection = Backbone.Collection.extend({
        initialize: function (id) {
            this.id = id;
        },
        url : function () {
            return ticketBackend('events/') + this.id + '/tickets';
        },
        model: TicketModel
    });


    function renderInto(parent) {
        return function (view) {
            view.render().$el.appendTo(parent.$el);
        };
    }

    function createRow(model) {
        return new Row({ model: model });
    }



    /*
        return table view which has collection of rows.
     */
    return View.extend({
        template: template,
        className: 'ticket-view-table',
        tagName: 'table',
        url: '',
        initialize: function (options) {
            this.ticketId = options.ticketId;
            this.open();
        },
        afterRender: function () {
            // FIXME
            Firefly.viewport.hideMessage();

            $('.ticket-view-table-wrapper').prepend(this.$el);

            if (this.collection) {
                this.collection.map(createRow).forEach(renderInto(this));
            }


            _.extend(controller, {
                scrollbar: {
                    $scroll: $('.ticket-view-table-scroll'),
                    $scrollKnob: $('.ticket-view-table-scroll-knob')
                },
                options: {
                    scrollingX: false
                }
            });
        },
        _open: function (done) {
            var $tableContainer = $('.ticket-view-table-wrapper'),
                $tableContent = $('.ticket-view-table');

            this.$el.css('opacity', 0);
            this.scroller = new Scroll($tableContainer, $tableContent, controller);

            done();
        },
        _close: function (done) {
            this.scroller.destroy();
            done();
        },
        _load: function (done) {
            var that = this;
            this.collection = new TicketCollection(this.ticketId);

            this.collection.reset([
                  {
                    "id": 9,
                    "type": "Зал",
                    "min_price": 260,
                    "max_price": 280,
                    "available": 55,
                    "seats": true
                  }
                ]);


            setTimeout(function () {
                $('.spinner').animate({
                        scale: 0.2,
                        opacity: 0
                    }, {
                        duration: 500,
                        done: function () {
                            $(this).remove();
                            $('.table-dots').css('opacity', 1);
                            that.$el.animate({opacity: 1});
                            done();
                        }
                    });

            }, 500);

        }
    });
});

