define('views/ModalView',[
    'framework/View',
    'framework/Container',
    'templates/modal-view'
], function (View, Container, template) {
    'use strict';

    return View.extend({
        className: 'modal-view',
        template: template,
        regions: {
            content: '.modal-view-container'
        },
        subviews: {
            container: Container
        },
        layout: {
            content: 'container'
        },
        events: {
            'click .modal-view-tab-bar-item-city': 'city',
            'click .modal-view-tab-bar-item-metro': 'metro'
        },
        initialize: function (options) {
            this._map = options.map;
            this._metro = options.metro;
        },
        afterRender: function () {
            this.$el.css({
                opacity: 0.0
            });

            if (this.current) {
                this.select(this.current);
            }
        },
        _open: function (done) {
            this.$el.animate({
                opacity: 1.0
            }, {
                complete: done
            });
        },
        _close: function (done) {
            this.$el.animate({
                opacity: 0.0
            }, {
                complete: done
            });
        },
        city: function () {
            if (this.current !== 'city') {
                this.select('city');
                this.subviews.container.show(this._map);
                this.current = 'city';
            }

            return this;
        },
        metro: function () {
            if (this.current !== 'metro') {
                this.select('metro');
                this.subviews.container.show(this._metro);
                this.current = 'metro';
            }

            return this;
        },
        select: function (tab) {
            if (this.current) {
                this.$('.modal-view-tab-bar-item-' + this.current).removeClass('modal-view-tab-bar-item-selected');
            }

            this.$('.modal-view-tab-bar-item-' + tab).addClass('modal-view-tab-bar-item-selected');
        }
    });
});
