define('views/ParkView',[
    'underscore',
    'jquery',
    'framework/View',
    'models/Product',
    'templates/product-view',
    'framework/utils/Scroll',
    'framework/utils/backend',
    'framework/utils/log',

    'bxslider'
], function (_, $, View, Product, template, Scroll, backend, log) {
    'use strict';

    return View.extend({
        className: 'product-view',
        template: template,
        i18n: true,
        events: {
            'click .back-button': 'back'
        },
        initialize: function () {
            this.$el.css({opacity: 0.0});
        },
        afterRender: function () {
            var images = $('ul.product-view-slider img'),
                i;

            function onLoad() {
                /*jshint validthis:true */
                var containerAspectRatio = $(this).parent().width() / $(this).parent().height(),
                    imageAspectRatio = this.width / this.height;

                if (containerAspectRatio < imageAspectRatio) {
                    $(this).css('width', '100%');
                    $(this).css('height', 'auto');
                }
                else {
                    $(this).css('width', 'auto');
                    $(this).css('height', '100%');
                }
            }

            for (i = 0; i < images.length; i++) {
                images[i].onload = onLoad;
            }
        },
        data : function () {
            return {
                templateId : 1,
                name : 'Нижний парк',
                description : 'Самым первым липецким парком был Нижний. Его заложили в 1805 году на месте Липецкого железоделательного завода. , расположен в самом центре города Липецка .На территории парка располагаются многочисленные аттракционы, увеселительные заведения, танцплощадка, открытые летние эстрады, действует колесо обозрения. ',
                locations : [
                    {
                        address : 'г. Липецк, проезд Петровский, д. 2',
                        // latitude : 56.142696,
                        // longitude : 40.412034
                    }
                ],
                // workTime: 'пн-вс 9:00–21:00',
                phones : [
                    '+7(4742) 271271'
                ],
                logo : '/images/no-image-logo.png',
                images : [
                    '/images/lipeck/image6.jpeg'
                ],
                // site: 'http://park-tambov.ru'
            };
        },
        _open: function (done) {
            this.printAvailable = false;
            this.printDiscountAvailable = false;

            // window.Firefly.trigger('map:locations:product', this.model.get('locations'));

            var that = this,
                $description = this.$('.product-view-description');
            // initialize scroller
            this.scroller = new Scroll($description, $description.find('article'), {
                scrollbar: {
                    $scroll: this.$('.product-view-description-scroll').css('opacity', 0),
                    $scrollKnob: this.$('.product-view-description-scroll-knob')
                },
                options: {
                    scrollingX: false
                }
            });

            // $('.bxslider').bxSlider({
            //     buildPager: function () {
            //         return '<div class="product-view-pager"></div>';
            //     },
            //     onSliderLoad: function () {

            //     }
            // });

            that.$el.css({opacity: 0.0, scale: 2.0}).animate({
                        opacity: 1.0,
                        scale: 1.0
                    }, {
                        complete: done
                    });

        },
        _close: function (done) {
            this.scroller.destroy();
            this.$el.animate({
                opacity: 0.0,
                scale: 2.0
            }, {
                complete: done
            });
        },
        back: function () {
            window.history.back();
        }
    });
});

