/*global define:false*/

define('framework/utils/handlebars',[
    'handlebars',
    'framework/utils/i18n',
    'framework/utils/backend', 
    'framework/utils/ticketBackend'
], function (Handlebars, i18n, backend, ticketBackend) {
    'use strict';
    Handlebars.registerHelper('i18n', i18n);
    function formatMinutes (date) {
        var minutes = date.getMinutes();
        return (minutes < 10 ? '0' : '') + minutes;
    }
    Handlebars.registerHelper('backend', backend);
    Handlebars.registerHelper('ticketBackend', ticketBackend);
    Handlebars.registerHelper('ticketFormatDate', function(str){
        var date = new Date(str),
            months = ['Января', 'Февраля', 'Марта', 'Апреля', 'Мая', 'Июня', 'Июля', 'Августа', 'Сентября', 'Октября', 'Ноября', 'Декабря'];
        return date.getDate() + ' ' + months[date.getMonth()] + ' ' 
            + date.getHours() + ':' 
            + formatMinutes(date);
    });
    Handlebars.registerHelper('equal', function (lvalue, rvalue, options) {
        if (arguments.length < 3) {
            throw new Error('Handlebars Helper equal needs 2 parameters');
        }
        if (lvalue !== rvalue) {
            return options.inverse(this);
        }
        return options.fn(this);
    });

    Handlebars.registerHelper('multiply', function () {
        var output = 1;
        Array.prototype.slice.call(arguments).forEach(function (number) {
            if (!isNaN(number)) output *= number
        });
        return output;
    });
    Handlebars.registerHelper('ticketsInclinations', function (amount) {
        +amount;
        if (amount == 1) return '';
        else if (amount >= 2 && amount <= 4) return 'а';
        else return 'ов';

    })


    Handlebars.registerHelper('findInHash', function (object, property) {
        return object[property]
    });

    Handlebars.registerHelper('paragraphize', function (text) {
        if (text) {
            var paragraphs = text.split('\n');
            return new Handlebars.SafeString('<p>' + paragraphs.join('</p><p>') + '</p>');
        }
    });
});
