/*global define:false,window:false*/

define('views/MetroView',[
    'jquery',
    'framework/View',
    'templates/metro-view'
], function ($, View, template) {
    'use strict';

    return View.extend({
        template : template,
        afterRender : function () {
            this.$('iframe').load(function () {
                $(this).contents().on('mousedown', function () {
                    window.Firefly.viewport.trigger('touch');
                });
            });

        }

    });
});
