define('framework/Container',[
    'underscore',
    'framework/View',
    'framework/utils/transition',
    'framework/utils/log'
], function (_, View, transition, log) {
    'use strict';

    //log = log('Container');

    return View.extend({
        className: 'container',
        initialize: function (options) {
            log.trace(_.result(this, 'id') + ' initialize');
            options = options || {};
            this.transitionOptions = options.transitionOptions || this.transitionOptions || 'none';
            this._locked = false;
            this._queue = [];
        },
        prepareView: function (view) {
            view.$el.addClass('container-item');
        },
        show: function (view, transitionOptions, done) {
            if (_.isFunction(transitionOptions)) {
                done = transitionOptions;
                transitionOptions = undefined;
            }

            transitionOptions = transitionOptions || this.transitionOptions;

            if (_.isString(transitionOptions)) {
                transitionOptions = {
                    name: transitionOptions
                };
            }

            // backwards compatibility
            done = done || transitionOptions.complete;

            // TODO коллбэки отброшенных записей
            this._queue[0] = { view : view, transitionOptions : transitionOptions, done : done };
            // this._queue.push({ view : view, transitionOptions : transitionOptions, done : done});

            if (!this._locked) {
                this._locked = true;
                this._next();
            }
        },
        _next: function () {
            var args = this._queue.shift(),
                container = this;

            if (!args) {
                this._locked = false;
                return;
            }

            this._show(args.view, args.transitionOptions, function () {
                if (args.done) {
                    args.done();
                }

                container._next();
            });
        },
        _show: function (view, transitionOptions, done) {
            log.trace('Showing view ' + view.id());

            var container = this;

            
            if (this.currentView === view) {
                log.trace('Showing view which is already shown');

                if (done) {
                    done();
                }

                return this;
            }

            this.prepareView(view);

            this.$el.append(view.$el);

            if (!this.currentView) {
                this.currentView = view;
                view.open(done);

                return this;
            }

            function makeTransition() {
                transition(container.currentView.$el, view.$el, transitionOptions, function () {
                    if (transitionOptions.beforeDetach) {
                        transitionOptions.beforeDetach();
                    }

                    container.currentView.$el.detach();
                    container.currentView = view;
                    container.currentView.open(done);
                });
            }

            this.currentView.close(function () {
                if (!view._rendered) {
                    if (view.state() === 'unloaded') {
                        view.load(function () {
                            view.render();
                            view._rendered = true;
                            makeTransition();
                        });
                    } else {
                        view.render();
                        view._rendered = true;
                        makeTransition();
                    }
                } else {
                    makeTransition();
                }
            });

            return this;
        }
    });
});
