define('views/ProductView',[
    'underscore',
    'jquery',
    'framework/View',
    'models/Product',
    'templates/product-view',
    'framework/utils/Scroll',
    'framework/utils/backend',
    'framework/utils/log',

    'bxslider'
], function (_, $, View, Product, template, Scroll, backend, log) {
    'use strict';

    return View.extend({
        className: 'product-view',
        template: template,
        i18n: true,
        events: {
            'click .back-button': 'back',
            'click .print-button': 'print',
            'click .print-discount-button': 'printDiscount'
        },
        initialize: function () {
            this.$el.css({opacity: 0.0});
        },
        _load: function (done) {
            this.model.fetch({
                success: done,
                error: function () {
                    throw new Error('can\'t load product view');
                }
            });
        },
        afterRender: function () {
            var images = $('ul.product-view-slider img'),
                i;

            function onLoad() {
                /*jshint validthis:true */
                var containerAspectRatio = $(this).parent().width() / $(this).parent().height(),
                    imageAspectRatio = this.width / this.height;

                if (containerAspectRatio < imageAspectRatio) {
                    $(this).css('width', '100%');
                    $(this).css('height', 'auto');
                }
                else {
                    $(this).css('width', 'auto');
                    $(this).css('height', '100%');
                }
            }

            for (i = 0; i < images.length; i++) {
                images[i].onload = onLoad;
            }
        },
        _open: function (done) {
            this.printAvailable = true;
            this.printDiscountAvailable = true;

            window.Firefly.trigger('map:locations:product', this.model.get('locations'));

            var that = this,
                $description = this.$('.product-view-description');
            // initialize scroller
            this.scroller = new Scroll($description, $description.find('article'), {
                scrollbar: {
                    $scroll: this.$('.product-view-description-scroll').css('opacity', 0),
                    $scrollKnob: this.$('.product-view-description-scroll-knob')
                },
                options: {
                    scrollingX: false
                }
            });

            $('.bxslider').bxSlider({
                buildPager: function () {
                    return '<div class="product-view-pager"></div>';
                },
                onSliderLoad: function () {
                    that.$el.css({opacity: 0.0, scale: 2.0}).animate({
                        opacity: 1.0,
                        scale: 1.0
                    }, {
                        complete: done
                    });
                }
            });

        },
        _close: function (done) {
            this.scroller.destroy();
            this.$el.animate({
                opacity: 0.0,
                scale: 2.0
            }, {
                complete: done
            });
        },
        back: function () {
            window.history.back();
        },
        print: function () {
            if (this.printAvailable) {
                $.ajax({
                    method: 'POST',
                    url: backend('print') + '/' + this.model.get('id') + '?lang=' + window.Firefly.lang()
                });

                log.event('product:print', { id: this.model.get('id')});
                this.printAvailable = false;
            }
        },
        printDiscount: function () {
            if (this.printDiscountAvailable) {
                $.ajax({
                    method: 'POST',
                    url: backend('print/discount') + '/' + this.model.get('id') + '?lang=' + window.Firefly.lang()
                });

                log.event('product:print_discount', { id: this.model.get('id')});
                this.printDiscountAvailable = false;
            }
        }
    });
});

