define('views/TicketConfirmView',[
    'underscore',
    'jquery',
    'framework/View',
    'templates/ticket-confirm-view',
    'ftscroller',
    'framework/utils/backend',
    'framework/utils/log',
    'bxslider',
    'models/ticketCart',
    'framework/utils/ticketBackend',
    'views/MessageView'
], function (_, $, View, template, FTScroller, backend, log, bxSlider, ticketCart, ticketBackend, MessageView) {
    'use strict';
    return View.extend({
        className: 'product-view ticket-view',
        template: template,
        events: {
            'click .ticket-confirm-view-back': 'back',
            'click .ticket-confirm-view-confirm': 'proceed',
            'click .ticket-confirm-view-repick': 'back'
        },
        initialize: function () {
            this.$el.css({opacity: 0.0, scale: 2.0});
        },
        _open: function (done) {
            this.$el.animate({
                opacity: 1.0,
                scale: 1
            }, {
                complete: done
            });
        },
        _close: function (done) {
            this.$el.animate({
                opacity: 0.0,
                scale: 2
            }, {
                complete: done
            });
        },
        data: function () {
            return _.extend(ticketCart.model.toJSON(),
            {
                emptyTicketsWithSeats: !ticketCart.ticketsWithSeatsN,
                emptyTicketsWithoutSeats: !ticketCart.ticketsWithoutSeatsN
            });
        },
        back: function () {
            window.history.back();
        },
        proceed: function () {
            var that = this,
                ticketModel;

            Firefly.viewport.showMessage(new MessageView({
                spinner: true,
                message: 'Пожалуйста, подождите. Запрос обрабатывается...',
                closeBtn: false
            }));

            $.ajax({
                method: 'post',
                dataType: 'json',
                contentType : 'application/json',
                url: ticketBackend('/cart'),
                data: JSON.stringify(ticketCart.format()),
                success: function (response) {
                    Firefly.viewport.hideMessage(function () {
                        ticketCart.id = response.id;
                        Firefly.navigate('main/buyTicket');
                    });
                },
                error: function (xhr) {
                    var selector = '',
                        json, seatId;
                    try {
                        json = JSON.parse(xhr.responseText);
                    } catch (e) {
                        Firefly.viewport.showMessage(new MessageView({
                            message: 'Произошла неизвестная ошибка. Пожалуйста, повторите попытку.'
                        }));
                        return;
                    }

                    json.tickets.forEach(function (ticket) {
                        if (!ticket.available) {
                            seatId = ticket['seat_id'];
                            selector = '[data-ticket-id=';
                            selector += seatId !== undefined ? seatId + '\\:' : '';
                            $(selector + ticket.id + ']').addClass('not-available-row');
                            ticketModel = ticketCart.getById(ticket.id, seatId);
                            ticketModel.collection.remove(ticketModel);
                        }
                    });

                    Firefly.viewport.showMessage(new MessageView({
                        message: 'не все билеты доступны'
                    }));

                    that.$el.find('.ticket-confirm-view-confirm').remove();
                }
            });
        }
    });
});
