define('views/MessageView',[
    'underscore',
    'jquery',
    'backbone',
    'framework/View',
    'templates/message-view'
], function (_, $, Backbone, View, template) {
    'use strict';

    var defaults = {
        img: 'images/bang.png',
        header: 'Внимание!',
        message: '',
        closeBtn: true,
        spinner: false
    };

    return View.extend({
        events: {
            'click .message-close': 'closeMessage'
        },
        template: template,
        initialize: function (options) {
            this.options = _.defaults(options, defaults);

            if (this.options.img && !(/^\/?images/.test(this.options.img))) {
                this.options.img += 'images';
            }
        },
        closeMessage: function () {
            Firefly.viewport.hideMessage();
        },
        data: function () {
            return this.options;
        },
        afterRender: function () {
            var dimensions = this.options.dimensions;

            this.$msg = this.$el.find('.message');

            this.$msg.css({
                opacity: 0.0,
                scale: 2.0
            });

            if (dimensions) {
                if (dimensions.width) {
                    this.$msg.css({
                        width: dimensions.width,
                        'margin-left': -dimensions.width / 2
                    });
                }
                if (dimensions.height) {
                    this.$msg.css({
                        height: dimensions.height,
                        'margin-top': -dimensions.height / 2
                    });
                }
            }
        },
        _open: function (done) {
            this.$msg.animate({
                opacity: 1.0,
                scale: 1.0
            }, {
                complete: done
            });
        },
        _close: function (done) {
            this.$msg.animate({
                opacity: 0.0,
                scale: 2.0
            }, {
                complete: done
            });
        }
    });
});
