/*global define:false*/

define('views/WeatherWidgetView',[
    'framework/View',
    'templates/weather-widget-view'
], function (View, template) {
    'use strict';

    var months = [
            'january',
            'february',
            'march',
            'april',
            'may',
            'june',
            'july',
            'august',
            'september',
            'october',
            'november',
            'december'
        ],
        days = [
            'sunday',
            'monday',
            'tuesday',
            'wednesday',
            'thursday',
            'friday',
            'saturday'
        ];

    function mapWeather (data) {
        var dateComponents = data.date.split('-'),
            date = new Date(dateComponents[0], dateComponents[1] - 1, dateComponents[2]);

        data.day = parseInt(dateComponents[2], 10);
        data.month = months[parseInt(dateComponents[1] - 1, 10)];
        data.wday = days[date.getDay()];
        return data;
    }

    return View.extend({
        className: 'weather-widget-view',
        template: template,
        i18n: true,
        initialize: function () {
            this.$el.css('opacity', 0.0);
        },
        data: function () {
            return [
                    {
                        "date": "2015-11-18",
                        "type": "snowy",
                        "humidity": "0",
                        "pressure": "755",
                        "temperature": {
                          "day": "0",
                          "morning": "0",
                          "afternoon": "0",
                          "evening": "0"
                        }
                      },
                      {
                        "date": "2015-11-19",
                        "type": "rainy",
                        "humidity": "+5",
                        "pressure": "755",
                        "temperature": {
                          "day": "+4",
                          "morning": "+1",
                          "afternoon": "+1",
                          "evening": "+5"
                        }
                      },
                      {
                        "date": "2015-11-20",
                        "type": "rainy",
                        "humidity": "+7",
                        "pressure": "755",
                        "temperature": {
                          "day": "+4",
                          "morning": "+1",
                          "afternoon": "+1",
                          "evening": "+5"
                        }
                      },
                      {
                        "date": "2015-11-21",
                        "type": "rainy",
                        "humidity": "+6",
                        "pressure": "755",
                        "temperature": {
                          "day": "+4",
                          "morning": "+1",
                          "afternoon": "+1",
                          "evening": "+5"
                        }
                      },
                      {
                        "date": "2015-11-22",
                        "type": "partly-rainy",
                        "humidity": "+5",
                        "pressure": "755",
                        "temperature": {
                          "day": "+4",
                          "morning": "+1",
                          "afternoon": "+1",
                          "evening": "+5"
                        }
                      }
                    ].map(mapWeather);
        },
        _open: function (done) {
            this.$el.animate({
                opacity: 1.0
            }, {
                complete: done
            });
        },
        _close: function (done) {
            this.$el.animate({
                opacity: 0.0
            }, {
                complete: done
            });
        }
    });
});

