define('framework/Screen',[
    'underscore',
    'backbone',
    'framework/View',
    'framework/utils/log'
], function (_, Backbone, View, log) {
    'use strict';

    return View.extend({
        className: 'screen',
        routes: {
        },
        initialize: function () {
            log.trace('Screen initialized');

            this._routes = [];

            _.each(this.routes, function (callback, route) {
                this._routes.push({
                    regexp: Backbone.Router.prototype._routeToRegExp(route),
                    callback: callback
                });
            }, this);
        },
        route: function (fragment) {
            var callback,
                args;

            _.each(this._routes, function (route) {
                if (route.regexp.test(fragment)) {
                    callback = route.callback;
                    args = Backbone.Router.prototype._extractParameters(route.regexp, fragment);
                }
            }, this);

            this[callback].apply(this, args);
        }
    });
});
