define('views/TicketView',[
    'underscore',
    'jquery',
    'framework/View',
    'templates/ticket-view',
    'framework/utils/Scroll',
    'framework/utils/backend',
    'framework/utils/log',
    'bxslider',
    'views/SearchView',
    'collections/_eventCollection',
    'models/ticketCart',
    'views/TicketViewTable',
    'views/MessageView',
    'models/Event'
], function (_, $, View, template, Scroll, backend, log, bxSlider, SearchView, _eventCollection, ticketCart, TicketViewTable, MessageView, Event) {
    'use strict';
    var scheme,
        Scheme = View.extend({

            events: {
                'click .close': 'hide'
            },
            hide: function () {
                Firefly.viewport.hideModal();
            },
            _close: function (done) {
                // close scheme only if it is opened
                var self = this;

                this.$el.removeClass('animateScheme');

                if (!Firefly.viewport.modal) {
                    Firefly.viewport.currentView.$el.removeClass('blockElement');
                }

                self.$('.close').animate({
                    opacity: 0
                }, {
                    done: function () {
                        $(this).css('display', 'none');
                    }
                });
                scheme.$el.one('webkitTransitionEnd', function () {
                    self.img.one('click', function () {
                        Firefly.viewport.showModal(self);
                    });

                    done();
                    Firefly.viewport.currentView.$el.prepend(scheme.$el);
                });
                scheme.img.one('webkitTransitionEnd', function ($evt) {
                    $evt.stopPropagation();
                });
            },
            _open: function (done) {
                var self = this;
                setTimeout(function () {
                    scheme.$el.one('webkitTransitionEnd', function () {
                        Firefly.viewport.currentView.$el.addClass('blockElement');
                        self.$('.close').css({display: 'block', opacity: 0}).animate({
                            opacity: 1
                        }, {
                            done: function () {
                                done();
                            }
                        });

                    });
                    scheme.$el.addClass('animateScheme');
                }, 0);
            }
        });
    return View.extend({
        className: 'ticket-view',
        template: template,
        events: {
            'click .back-button': 'back',
            'click button.ticket-view-buy-button': 'proceed'
        },
        initialize: function (options) {
            options = options || {};
            this.$el.css({opacity: 0.0, scale: 2.0});

            this.eventId = options.eventId;
            this.deltaY = 18; // delta to scroll numbers
            this.preventScroll = 0;
        },
        afterRender: function () {
            var $scheme = this.$('.ticket-view-scheme-image');


            $('.ticket-view-table-scroll').css('opacity', 0);


            //initialize popup
            //adjust popup position and dimensions to the image width and height
            scheme = new Scheme();
            _.extend(scheme, {
                $el: $scheme,
                img: $scheme.find('img'),
                closeBtn: $scheme.find('button')
            });
            scheme.$el.one('click', function () {
                Firefly.viewport.showModal(scheme);
            });
            scheme.img.on('load', function () {
                var regExp = /\#/,
                    width = this.naturalWidth > 963 ? 963 : this.naturalWidth,
                    height = width * this.naturalHeight / this.naturalWidth - 20;

                $('head').append('<style>.animateScheme{margin-left:-#; width:#; height:#;margin-top:-#</style>'
                                    .replace(regExp, (width + 20) / 2 + 'px !important') // set left position; 20 - border width
                                    .replace(regExp, width + 'px !important') // ... width
                                    .replace(regExp, height + 'px !important')
                                    .replace(regExp, height / 2 + 'px !important')
                );
            });
        },
        model : new Event({
                "id": 1,
                "name": "007: СПЕКТР. 19 ноября.",
                "date": "2015-11-19T13:00:00.026Z", //iso8601
                "images": [
                  "/images/lipeck/spectre.jpg" // картинка доступна по url `/images/1383921685710.jpg`
                ],
                "descriptions": "Зашифрованное послание из неизвестного источника выводит Бонда на след зловещей глобальной организации под кодовым названием СПЕКТР, в то время как М пытается спасти секретную разведывательную службу от ликвидации.",
                "fields": [ // Произвольные поля, дизайном предусмотрены только режиссер и актер, но их может и не быть
                  {
                    "name": "Мировая премьера",
                    "value": "26 октября 2015"
                  },
                  {
                    "name": "Режиссер",
                    "value": "Сэм Мендес"
                  },
                  {
                    "name": "Продолжительность",
                    "value": "148 мин"
                  }
                ],
                "venue": {
                  "name": "Киноплекс Молл",
                  "address": "г. Липецк, ул. Терешковой, д. 25б, ТРЦ «Малибу»",
                  // "location": [56.125666, 40.396008],
                  "image": "/images/lipeck/spectre1.jpg",
                  "seating_plan_image": "/images/zal.png"
                }
              }),
        _open: function (done) {
            var $description = this.$('.ticket-view-description');
            this.scroller = new Scroll($description, $description.find('p'), {
                scrollbar: {
                    $scroll: this.$('.ticket-view-description-scroll').css('opacity', 0),
                    $scrollKnob: this.$('.ticket-view-description-scroll-knob')
                },
                callback: function (left, top) {

                    if (top > 0) {
                        $description.css('box-shadow', 'inset 0px 17px 14px -17px');
                    } else {
                        $description.css('box-shadow', 'none');
                    }

                },
                options: {
                    scrollingX: false
                }
            });

            // //TODO refactor all the locations stuff
            // // window.Firefly.trigger('map:locations:ticket', this.model.get('venue').location);

            scheme.$el.css('opacity', 0);
            Firefly.viewport.currentView.$el.prepend(scheme.$el);
            scheme.$el.animate({
                opacity: 1
            }, 400);

            this.$el.animate({
                opacity: 1.0,
                scale: 1
            }, {
                complete: done
            });

        },
        _close: function (done) {
            this.scroller.destroy();
            this.ticketViewTable.close();
            this.$el.prepend(scheme.$el);
            this.$el.animate({
                opacity: 0.0,
                scale: 2
            }, {
                complete: done
            });

        },
         _load: function (done) {
            var view = this;

            if (!ticketCart.initialized) {
                    ticketCart.initialize(view.model);
            }

                view.ticketViewTable = new TicketViewTable({ticketId: view.model.id});

            setTimeout(done, 0);
        },
        back: function () {
            window.history.back();
        },
        proceed: function () {
              ticketCart.calculateTotalTickets();
            ticketCart.calculateTotalSum();
            if (!ticketCart.isEmpty()) {
              Firefly.viewport.showMessage(new MessageView({
                  message: 'Билеты забронированы'
              }));
            } else {
                Firefly.viewport.showMessage(new MessageView({
                    message: 'вы не выбрали ни одного билета' //'нельзя выбрать больше 6 билетов'
                }));
            }
        }
    });
});

