define('views/CinemaView',[
    'underscore',
    'jquery',
    'framework/View',
    'models/Product',
    'templates/product-view',
    'framework/utils/Scroll',
    'framework/utils/backend',
    'framework/utils/log',

    'bxslider'
], function (_, $, View, Product, template, Scroll, backend, log) {
    'use strict';

    return View.extend({
        className: 'product-view',
        template: template,
        i18n: true,
        events: {
            'click .back-button': 'back'
        },
        initialize: function () {
            this.$el.css({opacity: 0.0});
        },
        afterRender: function () {
            var images = $('ul.product-view-slider img'),
                i;

            function onLoad() {
                /*jshint validthis:true */
                var containerAspectRatio = $(this).parent().width() / $(this).parent().height(),
                    imageAspectRatio = this.width / this.height;

                if (containerAspectRatio < imageAspectRatio) {
                    $(this).css('width', '100%');
                    $(this).css('height', 'auto');
                }
                else {
                    $(this).css('width', 'auto');
                    $(this).css('height', '100%');
                }
            }

            for (i = 0; i < images.length; i++) {
                images[i].onload = onLoad;
            }
        },
        data : function () {
            return {
                templateId : 1,
                name : 'Киномакс-Акварель',
                description : 'инотеатр «Киномакс-Акварель» в Тамбове – это 5 стильных комфортабельных залов на 199, 117 и 102 места, оснащенных новейшим кинопроекционным оборудованием и суперсовременной акустической аппаратурой для полного погружения в любимое кино. Залы кинотеатра «Киномакс-Акварель» оборудованы новейшей технологией RealD. Уникальный современный дизайн кинотеатра создаст позитивную атмосферу и хорошее настроение гостям киноцентра «Киномакс-Акварель» в Тамбове. Кинотеатр в Тамбове располагает площадью и техническими возможностями для проведения мероприятий различного формата',
                locations : [
                    {
                        address : 'г. Тамбов, ул. Студенецкая наб., д. 20А, ТЦ «Акварель»',
                        // latitude : 56.13074,
                        // longitude : 40.358009
                    }
                ],
                // workTime: 'пн-сб 18:00–5:00; вс 12:00–5:00, перерыв 17:00–18:00',
                phones : [
                    '+7 (4752) 43-07-77'
                ],
                logo : '/images/kino_logo.jpg',
                images : [
                    '/images/kino-akvarel.jpg'
                ],
                site: 'http://kinomax.ru/aquarel'
            };
        },
        _open: function (done) {
            this.printAvailable = false;
            this.printDiscountAvailable = false;

            // window.Firefly.trigger('map:locations:product', this.model.get('locations'));

            var that = this,
                $description = this.$('.product-view-description');
            // initialize scroller
            this.scroller = new Scroll($description, $description.find('article'), {
                scrollbar: {
                    $scroll: this.$('.product-view-description-scroll').css('opacity', 0),
                    $scrollKnob: this.$('.product-view-description-scroll-knob')
                },
                options: {
                    scrollingX: false
                }
            });

            // $('.bxslider').bxSlider({
            //     buildPager: function () {
            //         return '<div class="product-view-pager"></div>';
            //     },
            //     onSliderLoad: function () {

            //     }
            // });

            that.$el.css({opacity: 0.0, scale: 2.0}).animate({
                        opacity: 1.0,
                        scale: 1.0
                    }, {
                        complete: done
                    });

        },
        _close: function (done) {
            this.scroller.destroy();
            this.$el.animate({
                opacity: 0.0,
                scale: 2.0
            }, {
                complete: done
            });
        },
        back: function () {
            window.history.back();
        }
    });
});

