/*jslint nomen:true*/
/*global define:false,window:false*/

define('views/LanguageWidgetView',[
    'jquery',
    'framework/View',
    'templates/language-widget-view',
    'framework/utils/log',

    'jquery.easing'
], function ($, View, template, log) {
    'use strict';

    return View.extend({
        className: 'language-widget-view',
        template: template,
        i18n: true,
        events: {
            'click .language-widget-view-item': 'onItemClicked'
        },
        onItemClicked: function (e) {
            log.trace('Language item clicked');
            // window.Firefly.lang($(e.currentTarget).attr('data-lang'));
        },
        afterRender: function () {
            var items = this.$('.language-widget-view-item');

            $(items[1]).css('z-index', 100);

            items.css({
                left: 471,
                opacity: 0.0
            });

            this.delegateEvents();
        },
        _open: function (done) {
            var items = this.$('.language-widget-view-item'),
                count = 0,
                duration = 400,
                _done = function () {
                    if (count === 1) {
                        done();
                    }
                    count += 1;
                };

            $(items).animate({
                opacity: 1.0
            }, {
                duration: 100
            });

            $(items[0]).animate({
                left: '-=230'
            }, {
                complete: _done,
                duration: duration,
                easing: 'easeOutBounce'
            });

            $(items[2]).animate({
                left: '+=230',
                opacity: 1.0
            }, {
                complete: _done,
                duration: duration,
                easing: 'easeOutBounce'
            });

        },
        _close: function (done) {
            var items = this.$('.language-widget-view-item');

            $(items).animate({
                opacity: 0.0,
                left: 450
            }, {
                duration: 300
            }).promise().done(done);
        }
    });
});

