define('views/NightClubView',[
    'underscore',
    'jquery',
    'framework/View',
    'models/Product',
    'templates/product-view',
    'framework/utils/Scroll',
    'framework/utils/backend',
    'framework/utils/log',

    'bxslider'
], function (_, $, View, Product, template, Scroll, backend, log) {
    'use strict';

    return View.extend({
        className: 'product-view',
        template: template,
        i18n: true,
        events: {
            'click .back-button': 'back'
        },
        initialize: function () {
            this.$el.css({opacity: 0.0});
        },
        afterRender: function () {
            var images = $('ul.product-view-slider img'),
                i;

            function onLoad() {
                /*jshint validthis:true */
                var containerAspectRatio = $(this).parent().width() / $(this).parent().height(),
                    imageAspectRatio = this.width / this.height;

                if (containerAspectRatio < imageAspectRatio) {
                    $(this).css('width', '100%');
                    $(this).css('height', 'auto');
                }
                else {
                    $(this).css('width', 'auto');
                    $(this).css('height', '100%');
                }
            }

            for (i = 0; i < images.length; i++) {
                images[i].onload = onLoad;
            }
        },
        data : function () {
            return {
                templateId : 1,
                name : 'Культурно-развлекательный центр «Евразия»',
                description: 'Культурно-развлекательный центр «Евразия» в Липецке с огромным выбором блюд русской и европейской кухни, высококвалифицированными поварами, вкуснейшими разнообразными коктейлями, изысканным интерьером. Вы можете расслабиться, почувствовать теплоту и уют нашего заведения расположившись по интересам в одним из изящных залов : ресторан, vip зал, лаунж кафе, кальянная, ночной клуб и караоке, а также вспомнить все свои любимые музыкальные композиции, песни зарубежных и российских исполнителей. В нашем каталоге более 30000 песен на любой вкус! В культурно-развлекательном центре «Евразия» Вы можете провести: дни рождения, выпускные, любые вечеринки в соответствии с вашими желаниями и предпочтениями.',
                locations : [
                    {
                        address : 'г. Липецк, ул. Ленина, 11',
                        // latitude : 56.169132,
                        // longitude : 40.395576
                    }
                ],
                workTime: 'чт-сб 12:00-06:00, вс',
                phones : [
                    '+7 (4742) 271533',
                    '+7 (4742) 270246'
                ],
                logo : '/images/no-image-logo.png',
                images : [
                    // '/images/zd_vok_1.jpg',
                    '/images/lipeck/image2.jpeg'
                ],
                // site: 'http://club33.ru'
            };
        },
        _open: function (done) {
            this.printAvailable = false;
            this.printDiscountAvailable = false;

            // window.Firefly.trigger('map:locations:product', this.model.get('locations'));

            var that = this,
                $description = this.$('.product-view-description');
            // initialize scroller
            this.scroller = new Scroll($description, $description.find('article'), {
                scrollbar: {
                    $scroll: this.$('.product-view-description-scroll').css('opacity', 0),
                    $scrollKnob: this.$('.product-view-description-scroll-knob')
                },
                options: {
                    scrollingX: false
                }
            });

            // $('.bxslider').bxSlider({
            //     buildPager: function () {
            //         return '<div class="product-view-pager"></div>';
            //     },
            //     onSliderLoad: function () {

            //     }
            // });

            that.$el.css({opacity: 0.0, scale: 2.0}).animate({
                        opacity: 1.0,
                        scale: 1.0
                    }, {
                        complete: done
                    });

        },
        _close: function (done) {
            this.scroller.destroy();
            this.$el.animate({
                opacity: 0.0,
                scale: 2.0
            }, {
                complete: done
            });
        },
        back: function () {
            window.history.back();
        }
    });
});

