/*global define:false*/

define('framework/utils/i18n',[],function () {
    'use strict';

    var application,
        i18n = function (key) {
            return application.config('dictionaries')[application.lang()][key] || key;
        };

    i18n.init = function (app) {
        application = app;
    };

    return i18n;
});
