define('views/CategoryListView',[
    'jquery',
    'framework/View',
    'views/CentralBannerView',
    'templates/category-list-view',
    'framework/utils/log',
    'framework/utils/async',

    'jquery.transit',
    'jquery.easing'
], function ($, View, CentralBannerView, template, log, async) {
    'use strict';

    var radius = 300,
        angle = 1.75 * Math.PI,
        start = - (angle - Math.PI) / 2,
        duration = 1000;

    function angleStep(angle, fx) {
        var dx, dy;

        if (fx.prop === 'angle') {

            dx = -Math.floor(radius * Math.cos(angle));
            dy = -Math.floor(radius * Math.sin(angle));

            $(fx.elem).css({translate: [dx, dy]});
        }
    }

    return View.extend({
        className: 'category-list-view',
        template: template,
        i18n: true,
        initialize: function () {
            var showTickets = Firefly.config('showTickets');

            if ('string' === typeof showTickets) {
                showTickets = showTickets === 'true';
            }
        },
        regions: {
            center: '.central-banner-container'
        },
        subviews: {
            banner: CentralBannerView
        },
        layout: {
            center: 'banner'
        },
        data : function () {
            return [
                 {
                    "id": 585,
                    "name": "Отели",
                    "icon": "/images/hotels.png",
                    "url": "#main/hotel-categories",
                    "local": false
                  },
                  {
                    "id": 583,
                    "name": "Досуг",
                    "icon": "/images/dosug.png",
                    "url": "#main/dosug",
                    "local": false
                  },
                  {
                    "id": 586,
                    "name": "Питание",
                    "icon": "/images/food.png",
                    "url": "#main/food",
                    "local": false
                  },
                  {
                    "id": 581,
                    "name": "Культура",
                    "icon": "/images/culture.png",
                    "url": "#main/culture",
                    "local": false
                  },
                  // {
                  //   "id": 587,
                  //   "name": "Туры",
                  //   "icon": "/images/tours.png",
                  //   "local": false
                  // },
                  {
                    "id": 582,
                    "name": "Транспорт",
                    "icon": "/images/transport.png",
                    "url": "#main/transport",
                    "local": false
                  },
                  {
                    "id": 582,
                    "name": "Магазины",
                    "icon": "/images/shops.png",
                    "url": "#main/shops",
                    "local": false
                  }
                  // {
                  //   "id": 582,
                  //   "name": "Маршруты",
                  //   "icon": "/images/ways.png",
                  //   "local": false
                  // }
            ];
        },
        events: {
            'click .category-list-view-item-a': 'logEvent'
        },
        afterRender: function () {
            this._items = this.$('.category-list-view-item:not(.category-list-view-item-local)');
            this._local  = this.$('.category-list-view-item-local');
            this._step = angle / (this._items.length - 1);

            this._items.css({opacity: 0.0, left: 470, top: 399});
            this._local.css({opacity: 0.0, left: 489, top: 648});
        },
        _open: function (done) {
            var step = this._step,
                promises = [],
                that = this;

            this._items.each(function (index, element) {
                element.angle = start;

                promises.push($(element).animate({
                    angle: start + step * index,
                    opacity: 1.0
                }, {
                    duration: duration,
                    step: angleStep
                }).promise());
            });

            promises.push(this._local.animate({
                opacity: 1.0,
                top: 700
            }).promise());

            async.parallel([ function (done) {
                    $.when.apply($, promises).done(done);
                }, function (done) {
                    that.subviews.banner.open(done);
                }], done);
        },
        _close: function (done) {
            var promises = [];

            this._items.each(function (index, element) {
                promises.push($(element).animate({
                    angle: start,
                    opacity: 0.0
                }, {
                    duration: duration,
                    step: angleStep
                }).promise());
            });

            promises.push(this._local.animate({
                opacity: 0.0,
                top: 300
            }).promise());

            async.parallel([ function (done) {
                    $.when.apply($, promises).done(done);
                }, this.closeSubviews.bind(this) ], done);
        }
    });
});

