define('framework/utils/transition',[
    'underscore',
    'framework/utils/async',

    'jquery.transit'
], function (_, async) {
    'use strict';

    function transition ($old, $new, options, callback) {
        var name = options.name;

        transition[name]($old, $new, options, callback);
    }

    _.extend(transition, {
        none: function ($old, $new, options, done) {
            done();
        },
        slide: function ($old, $new, options, done) {
            var direction = options.direction || 'left',
                duration = options.duration || 400,
                reverse = options.reverse || false,

                property,
                oldFrom = 0,
                oldTo,
                newFrom,
                newTo = 0,
                oldToCss = {},
                newToCss = {};

            if (direction === 'left' || direction === 'right') {
                property = 'left';
            } else if (direction === 'up' || direction === 'down') {
                property = 'top';
            }

            if (direction === 'left' || direction === 'up') {
                oldTo = '-100%';
                newFrom = '100%';
            } else if (direction === 'right' || direction === 'down') {
                oldTo = '100%';
                newFrom = '-100%';
            }

            oldToCss[property] = oldTo;
            newToCss[property] = newTo;


            $old.css(property, oldFrom);
            $new.css(property, newFrom);

            async.parallel([ function (done) {
                $old.animate(oldToCss, duration, done);
            }, function (done) {
                $new.animate(newToCss, duration, done);
            }], done);
        },
        'slide-css': function ($old, $new, options, callback) {
            var direction = options.direction || 'left',
                duration = options.duration || 400,
                reverse = options.reverse || false,

                property,
                oldFrom = 0,
                oldTo,
                newFrom,
                newTo = 0,
                oldToCss = {},
                newToCss = {};

            if (direction === 'left' || direction === 'right') {
                property = 'x';
            } else if (direction === 'up' || direction === 'down') {
                property = 'y';
            }

            if (direction === 'left' || direction === 'up') {
                oldTo = '-100%';
                newFrom = '100%';
            } else if (direction === 'right' || direction === 'down') {
                oldTo = '100%';
                newFrom = '-100%';
            }

            oldToCss[property] = oldTo;
            newToCss[property] = newTo;


            $old.css(property, oldFrom);
            $new.css(property, newFrom);

            $old.transition(oldToCss, _callback);
            $new.transition(newToCss, _callback);
        },
        fade: function ($old, $new, options, callback) {
            var done = false,
                _callback = function () {
                    if (done && callback) {
                        callback();
                    } else {
                        done = true;
                    }
                },
                duration = options.duration || 200;

            $old.css('opacity', 1);
            $new.css('opacity', 0);

            $old.animate({opacity: 0}, {duration: duration, complete: _callback});
            $new.animate({opacity: 1}, {duration: duration, complete: _callback});
        },
        scale: function ($old, $new, options, callback) {
            $new.css({scale: 0.1, opacity: 0})
                .transition({scale: 1, opacity: 1}, callback);
        }
    });

    return transition;
});
