/*jslint nomen:true*/
/*global define:false*/

define('framework/utils/keyboard',[
    'jquery',
    'underscore'
], function ($, _) {
    'use strict';

    var keyboard = {
        layouts: {
            'ru': [
                [1,2,3,4,5,6,7,8,9,0, 'backspace'],
                ['й', 'ц', 'у', 'к', 'е', 'н', 'г', 'ш', 'щ', 'з', 'х', 'ъ'],
                ['ф', 'ы', 'в', 'а', 'п', 'р', 'о', 'л', 'д', 'ж', 'э', 'ё', 'search'],
                ['я', 'ч', 'с', 'м', 'и', 'т', 'ь', 'б', 'ю', 'hide', 'layout'],
                ['space']
            ],
            'en': [
                [1,2,3,4,5,6,7,8,9,0, 'backspace'],
                ['q', 'w', 'e', 'r', 't', 'y', 'u', 'i', 'o', 'p'],
                ['a', 's', 'd', 'f', 'g', 'h', 'j', 'k', 'l', 'search'],
                ['z', 'x', 'c', 'v', 'b', 'n', 'm', '#', 'hide', 'layout'],
                ['-', '_','space', '@', '.']
            ]
        },
        defaultLayout: 'ru',
        offset: 100,
        buttons: {
            hide: {
                cls: 'keyboard-button-hide',
                action: function () {
                    keyboard.hide();
                },
                display: function (layout) {
                    return layout === 'ru' ? 'скрыть' : 'hide';
                }
            },
            space: {
                cls: 'keyboard-button-space',
                type: ' ',
                display: ' '
            },
            backspace: {
                cls: 'keyboard-button-backspace',
                action: function () {
                    var value = keyboard.$input.val();

                    if (value.length > 0) {
                        keyboard.$input.val(value.slice(0, -1)).change();
                    }

                    keyboard.placeCursor();

                },
                display: '←',
            },

            number: {
                action: function act (key) {
                    if (isNaN(key)) {
                        if (key === 'backspace') {
                            key = '';
                        } else if (key === 'hide'){
                            keyboard.hide();
                        } else {
                            return;
                        }
                        
                    }
                    var that = keyboard.buttons.number,
                        value = keyboard.$input.val().replace(/\D/g,'').slice(1, key ? 12 : -1), 
                        position = value.length;
                    
                    keyboard.$input.val(that.applyMask(value + key)).change();
                    
                    keyboard.placeCursor();
                    },
                applyMask: function (number) {
                    console.log(number);
                    return '+7 ' + number.replace(/(\d{3})(\d{3}){0,1}(\d{2}){0,1}(\d{2}){0,1}/, '($1) $2 $3 $4')
                                .slice(0, 15)
                                .replace(/\s*$/g, ''); // right trim
                    }
            },
            layout: {
                cls: 'keyboard-button-layout',
                action: function (lang) {
                    var layout = keyboard.currentLayout === 'ru' ? 'en' : 'ru';

                    keyboard.layout(('' + lang.match(/en|ru/) === 'null') ? layout : lang);
                },
                display: 'eng/рус'
            },
            search: {
                cls: 'keyboard-button-search',
                action: function () {
                    // keyboard.$input.parents('form').submit();
                    // keyboard.hide();
                },
                display: function (layout) {
                    return layout === 'ru' ? 'поиск' : 'search';
                }
            }
        },
        selector: 'input[type=text], input[type=tel]',
        init: function ($el) {
            this.$el = this.createKeyboard();
            this.cacheLayouts();

            this.layout(this.defaultLayout);

            $el.append(this.$el)
                .on('focusin', this.selector, function (event) {
                    keyboard.$input = $(this);

                    keyboard.$input.on('focusout', function () {
                        keyboard.hide();
                    });

                    keyboard.show(event.target);
                });
        },
        cacheLayouts: function () {
            var key,
                layout;

            this.cachedLayouts = {};

            for (key in this.layouts) {
                if (this.layouts.hasOwnProperty(key)) {
                    layout = this.layouts[key];

                    this.cachedLayouts[key] = this.createLayout(layout, key);
                }
            }
        },
        createButton: function (key, layout) {
            var options = this.buttons[key] || {},
                display = options.display,
                $el;

            if (!display) {
                display = key;
            }

            if ('function' === typeof display) {
                display = display(layout);
            }

            $el = $('<button class="keyboard-button"></button>')
                .attr('data-action', key)
                .text(display);

            if (options.cls) {
                $el.addClass(options.cls);
            }

            return $el;
        },
        createRow: function (keys, layout) {
            var $el = $('<div class="keyboard-row"></div>');

            _.each(keys, function (key) {
                $el.append(keyboard.createButton(key, layout));
            });

            return $el;
        },
        createLayout: function (layout, name) {
            var $el = $('<div class="keyboard-layout"></div>');

            _.each(layout, function (row) {
                $el.append(keyboard.createRow(row, name));
            });

            return $el;
        },
        createKeyboard: function () {
            var $el = $('<div class="keyboard keyboard-hidden"></div>');

            $el
                .on('click', '.keyboard-button', function (event) {
                    var key = $(event.target).attr('data-action');

                    keyboard.processKey(key);
                })
                .on('mousedown', function (event) {
                    event.preventDefault();
                });

            return $el;
        },
        processKey: function (key) {
            console.log();
            var options = (this.$input.attr('type') === 'tel' ? this.buttons.number : 0) || this.buttons[key] || {},
                type = options.type || key,
                action = options.action || function () {
                    keyboard.type(type);
                };
            action(key);
        },
        type: function (text) {
            var value = this.$input.val();

            this.$input.val(value + text).change();
            this.placeCursor();
        },
        placeCursor: function (position) {
            var stop = position || this.$input.val().length;

            this.$input[0].selectionStart = this.$input[0].selectionEnd = stop;
        },
        show: function (input) {
            this.$input = $(input);
            switch(input.className)
            {
            case 'email-input':
              // execute code block 1
              this.buttons.layout.action('en')
              break;
            case 'credentials-input':
              // execute code block 2
              this.buttons.layout.action('ru')
              break;
            }

            var top = this.$input.offset().top;

            this.$el.removeClass('keyboard-hidden')
                .addClass('keyboard-visible')
                .css('top', top + this.offset);
        },
        hide: function () {
            if (this.$input) {
                this.$input[0].blur(); //jquery's blur() fails for some reason
                this.$input = null;
            }

            this.$el.removeClass('keyboard-visible')
                .addClass('keyboard-hidden');
        },
        layout: function (layout) {
            var $layout = this.cachedLayouts[layout],
                that = this;

            that.$el.empty().append($layout);

            this.currentLayout = layout;
        }
    };

    return keyboard;
});
