define('views/CentralBannerView',[
    'framework/View',
    'templates/central-banner-view'
], function (View, template) {
    'use strict';

    return View.extend({
        className: 'central-banner-view',
        template: template,

        events: {
            'click .central-banner-a': 'logEvent'
        },
        initialize: function () {
            this.$el.css({
                opacity : 0.0,
                perspective: '1000px',
                rotateY: '180deg'
            });
        },
        data: function () {
            return {
                image : '/images/central.png',
                productId : 1
            };
        },
        _open: function (done) {
            this.$el.transition({
                opacity : 1.0,
                rotateY: '0deg'
            }, done);
        },
        _close: function (done) {
            this.$el.transition({
                opacity : 0.0,
                rotateY: '180deg'
            }, done);
        }
    });
});
