define('framework/utils/Scroll',[
    'jquery',
    'framework/utils/scroller'
], function ($, Scroller) {



    function Scroll ($container, $content, controller) {
        //Scroll is a wrapper for zynga scroller. Basicaly it is inheritated from Scroller class.
        //Thus all methods of Scroller prototype can be used.
        // {
        //     callback: [Function], to be called as zynga scroller callback
        //     scrollbar: { - For standard scrollbar (vertical text)
        //         $scroll: [jquery object],
        //         $scrollKnob: [jquery object]
        //     },
        //     activeView: [Object], containing rollBack method to deactive current element
        //     options: [Object], options for zynga scroller
        //     deltaWidth: [number], - adds (reduces) additional width allowing to set padding of container
        //     deltaHeight: [number] - the same for the height
        // }
        var movement = {
            deltaConstraint: 10,
            curX: 0,
            curY:  0,
            startX: 0,
            startY: 0,
            calcDelta: function () {
                return Math.sqrt(Math.pow(this.curY - this.startY, 2) + Math.pow(this.curX - this.startX, 2));
            }
        };

        controller = controller || {};

        var dh = controller.deltaHeight || 0,
            dw = controller.deltaWidth || 0,
            that = this,
            scrollbarFn = controller.scrollbarFn,
            $body = $('body'),
            mousedown;

        Scroller.call(this, function  (left, top, zoom) {
            if (controller.callback) {
                controller.callback(left, top, zoom);
            }

            $content.css({
                    translate: [-left, -top]
                });

            // handle the scroll bar if it exists
            if (scrollbarFn) {
                scrollbarFn(left, top);
            }

            }, controller.options || {});

        this.setDimensions($container.outerWidth(), $container.outerHeight(), $content.outerWidth() + dw, $content.outerHeight() + dh);

        function mouseDown (e) {
            if (!e.fromElement) {

                movement.startX = e.pageX;
                movement.startY = e.pageY;

                that.doTouchStart([{
                    pageX: e.pageX,
                    pageY: e.pageY
                }], e.timeStamp);

                mousedown = true;
            }

            e.preventDefault();
        }

        function mouseMove (e) {

            if (!mousedown) {
                return;
            }

            movement.curX = e.pageX;
            movement.curY = e.pageY;

            if (controller.activeView && controller.activeView.rollBack && movement.calcDelta() >= movement.deltaConstraint) {
                controller.activeView.rollBack();
            }

            that.doTouchMove([{
                pageX: e.pageX,
                pageY: e.pageY
            }], e.timeStamp);

            mousedown = true;
        }

        function mouseUp (e) {

            if (!mousedown) {
                return;
            }

            if (!e.fromElement) {
                that.doTouchEnd(e.timeStamp);
                mousedown = false;
            }
        }

        $container.on('mousedown', mouseDown);
        $body.on('mousemove', mouseMove);
        $body.on('mouseup', mouseUp);


        if ($container.height() >= $content.height() && $container.width() >= $content.width()) {
            this.options.scrollingY = this.options.scrollingX = false;
        }

        if (controller.scrollbar) {

            scrollbarFn = (function ($scroll, $scrollKnob) {
                var scrollHeight = $scroll.height(),
                    containerHeight = $container.height(),
                    contentHeight = $content.height(),
                    knobHeight = (containerHeight / contentHeight) * scrollHeight;

                if (contentHeight <= containerHeight) {
                    $scroll.hide();
                    return;
                }

                $scrollKnob.css('height', knobHeight);
                $scroll.animate({opacity: 1});

                return function (left, top) {
                    $scrollKnob.css({
                        translate: [0, (top / contentHeight) * scrollHeight]
                    });
                };
            })(controller.scrollbar.$scroll, controller.scrollbar.$scrollKnob);
        }

        this.destroy = function () {
            $container.off('mousedown', mouseDown);
            $body.off('mousemove', mouseMove);
            $body.off('mouseup', mouseUp);
        };
    }

    Scroll.prototype.destroy = function () {}

    for (var key in Scroller.prototype) {
        Scroll.prototype[key] = Scroller.prototype[key];
    }

    return Scroll;

});

