/*jslint nomen:true*/
/*global define:false, console:false*/

define('framework/utils/log',[
    'jquery',
    'underscore',
    'framework/utils/backend'
], function ($, _, backend) {
    'use strict';

    var log = {
    };

    _.each([
        'trace',
        'debug',
        'info',
        'warn',
        'error'
    ], function (type) {
        log[type] = function (message) {
            console.log('[' + new Date().toTimeString() + '] [' + type.toUpperCase() + '] ' + message);
        };
    });

    log.event = function (event, options) {
    };

    log.sendUserActivity = function(e){
        var id = $(e.currentTarget).data("id");
        var event = $(e.currentTarget).data("event");
        console.log("send user activity " + event + " with " + id);
        this.event(event,{id: id});
    };

    return log;
});
