define('views/TicketBuyView',[
    'jquery',
    'underscore',
    'framework/View',
    'templates/ticket-buy-view',
    'models/ticketCart',
    'framework/utils/ticketBackend',
    'views/MessageView',
    'framework/utils/ticketBackend'
], function ($, _, View, template, ticketCart, ticketBackend, MessageView, backend) {
    'use strict';

    return View.extend({
        events: {
            'click .ticket-buy-view-main-keypad button': 'keypadPress',
            'click input': 'setActiveInput',
            'click .ticket-buy-view-main-phone-btn': 'getPhoneId',
            'click .ticket-buy-view-confirm-button': 'proceed'
        },
        className: 'product-view ticket-view',
        template: template,
        _load: function (done) {
            this.model = ticketCart.model;
            done();
        },
        setActiveInput: function (evt) {
            this.$activeInput = $(evt.fromElement);
        },
        initialize: function () {
            _.bindAll(this, 'getPhoneId');
            this.$el.css({opacity: 0.0, scale: 2.0});
        },
        _open: function (done) {
            this.$el.animate({
                opacity: 1.0,
                scale: 1
            }, {
                complete: done
            });
        },
        _close: function (done) {
            this.$el.animate({
                opacity: 0.0,
                scale: 2
            }, {
                complete: done
            });
        },
        afterRender: function () {
            this.inputBlocks = {
                phone: {
                    $input: this.$('input[type=tel]'),
                    message: this.$('.phone-input-message'),
                    re: new RegExp(/[\w\W]{18}/)
                },
                credentials: {
                    $input: this.$('.credentials-input'),
                    message: this.$('.credentials-input-message'),
                    re: /[\w\W]{1}/
                },
                email: {
                    $input: this.$('.email-input'),
                    message: this.$('.email-input-message'),
                    re: /^([0-9a-zA-Z]+[\-._+&amp;])*[0-9a-zA-Z]+@([\-0-9a-zA-Z]+[.])+[a-zA-Z]{2,6}$/ // http://regexlib.com/DisplayPatterns.aspx
                }
            };
            this.$proceedButton = this.$('.ticket-buy-view-confirm-button').addClass('disabled');
            this.$codeInput = this.$('.ticket-buy-view-main-code input');
        },
        checkFields: function () {
            var prop,
                inputBlock,
                inputBlocks = this.inputBlocks,
                proceed = true;
            for (prop in inputBlocks) {
                inputBlock = inputBlocks[prop];
                if (inputBlock.re.test(inputBlock.$input.val())) {
                    inputBlock.message.css('opacity', 0);
                } else {
                    inputBlock.message.css('opacity', 1);
                    proceed = false;
                }
            }
            return proceed;

        },
        getPhoneId: function () {
            if (this.checkFields()) {
                this.phoneNmbr = this.inputBlocks.phone.$input.val();

                var $button = this.$('[class*=phone-btn]'),
                    INTERVAL = 180,
                    $timer = this.$('[class*=timer]').css('opacity', 1).html(INTERVAL + ' сек.'),
                    $message = $('.ticket-buy-view-main-wait-message'),
                    currentTime = (new Date()).getTime(),
                    interval = setInterval(function () {
                        var timeSpent = Math.round((Date.now() - currentTime) / 1000);
                        $timer.html((INTERVAL - timeSpent) + ' сек.');
                        if (timeSpent >= INTERVAL) {
                            clearInterval(interval);
                            $timer.css('opacity', 0);
                            $message.animate({
                                opacity: 0
                            }, {
                                duration: 500,
                                done: function () {
                                    $button.animate({opacity: 1}).removeClass('disabled');
                                }
                            });
                        }
                    }, 1000);


                $button.animate({
                    opacity:0
                }, {
                    duration: 500,
                    done: function () {
                        $message.animate({
                            opacity: 1
                        });
                    }
                }).addClass('disabled');

                this.$proceedButton.removeClass('disabled');
                $.ajax({
                    method: 'post',
                    url: ticketBackend('/phones'),
                    dataType: 'json',
                    contentType : 'application/json',
                    data: JSON.stringify({
                        phone: this.phoneNmbr
                    }),
                    success: function (response) {
                        this.phoneId = response.id;
                        Firefly.sessionManager.session.prolong(INTERVAL * 1000);
                    },
                    context: this
                });

                this.$activeInput = $('.ticket-buy-view-main-code-holder');
            }
        },
        proceed: function () {
            Firefly.viewport.showMessage(new MessageView({
                spinner: true,
                message: 'пожалуйста, подождите. Идет обработка...',
                closeBtn: false
            }));
            $.ajax({
                method: 'get',
                url: ticketBackend('/phones/' + this.phoneId + '?' + 'code=' + this.$codeInput.val()),
                success: function () {
                    var json = {
                            'cart_id': ticketCart.id,
                            phone: this.phoneNmbr,
                            name: this.inputBlocks.credentials.$input.val(),
                            email: this.inputBlocks.email.$input.val()
                        };
                    $.ajax({
                        method: 'post',
                        url: ticketBackend('/order'),
                        contentType : 'application/json',
                        data: JSON.stringify(json),
                        success: function (response) {
                            ticketCart.model.set({
                                cartId: response.id,
                                ttl: response.ttl
                            });

                            var statistics = ticketCart.formatStatistics();
                            _.extend(statistics, {
                                'customer_name': json.name,
                                'customer_phone': json.phone,
                                'customer_email': json.email,
                                'order_id': response.orderId
                            });

                            Firefly.viewport.hideMessage(function () {
                                Firefly.navigate('main/successTicket');
                            });

                            $.ajax({
                                method: 'post',
                                url: backend('/stats/tickets'),
                                contentType : 'application/json',
                                data: JSON.stringify(statistics)
                            });
                        },
                        error: function () {
                        },
                        context: this
                    });

                },
                error: function () {
                    Firefly.viewport.hideMessage();
                    this.$('.code-input-message').css('opacity', 1);
                },
                context: this
            });
        }
    });
});

