define('views/SamohvalView',[
    'underscore',
    'jquery',
    'framework/View',
    'models/Product',
    'templates/product-view',
    'framework/utils/Scroll',
    'framework/utils/backend',
    'framework/utils/log',

    'bxslider'
], function (_, $, View, Product, template, Scroll, backend, log) {
    'use strict';

    return View.extend({
        className: 'product-view',
        template: template,
        i18n: true,
        events: {
            'click .back-button': 'back'
        },
        initialize: function () {
            this.$el.css({opacity: 0.0});
        },
        afterRender: function () {
            var images = $('ul.product-view-slider img'),
                i;

            function onLoad() {
                /*jshint validthis:true */
                var containerAspectRatio = $(this).parent().width() / $(this).parent().height(),
                    imageAspectRatio = this.width / this.height;

                if (containerAspectRatio < imageAspectRatio) {
                    $(this).css('width', '100%');
                    $(this).css('height', 'auto');
                }
                else {
                    $(this).css('width', 'auto');
                    $(this).css('height', '100%');
                }
            }

            for (i = 0; i < images.length; i++) {
                images[i].onload = onLoad;
            }
        },
        data : function () {
            return {
                templateId : 1,
                name : 'ДРЦ «Остров капитана Флинта»',
                description : 'Мир, где различия в возрасте не имеют значения! Детский развлекательный центр предлагает Вам весело и познавательно провести время в незабываемой атмосфере доброго праздника. Вас жду Вас жду игровые аттракционы «Сокровища Бена Гана», детский лабиринт «Джунгли», боулинг «Золотое ядро» и бильярдная «Адмирал», рассчитанных на любой возраст. Не так давно на территории развлекательного центра был открыт кинотеатр «Флинт», в котором поддерживается формат 3D и 5D. Зрелище великолепное. Это место стоит посетить! Если Вы не знаете, где отметить день рождения своего чада, развлекательный центр всегда к Вашим услугам. На территории есть кафе «Таверна Джона Сильвера», кафе «Гриль-Хаус» и пиццерия «Пиратопицца» Взрослые могут сделать покупки или просто прогуляться на территории торгового центра. В выходные время работы центра увеличено для удобства посетителей.',
                locations : [
                    {
                        address : 'г. Липецк, ул. Студеновская, 124а, ТРЦ Липецк, 3 этаж'
                        // latitude : 56.132875,
                        // longitude : 40.416408
                    }
                ],
                workTime: 'понедельник-четверг 10:00-23:00; пятница, суббота 10:00-01:00; воскресенье 10:00-23:00',
                phones : [
                    '+7(4742) 364488',
                    '+7(4742) 483686',
                    '+7(4742) 391217'
                ],
                logo : '/images/no-image-logo.png',
                images : [
                    // '/images/zd_vok_1.jpg',
                    '/images/lipeck/image1.jpeg'
                ],
                site: 'http://www.ostrov-flinta.ru'
            };
        },
        _open: function (done) {
            this.printAvailable = false;
            this.printDiscountAvailable = false;

            // window.Firefly.trigger('map:locations:product', this.model.get('locations'));

            var that = this,
                $description = this.$('.product-view-description');
            // initialize scroller
            this.scroller = new Scroll($description, $description.find('article'), {
                scrollbar: {
                    $scroll: this.$('.product-view-description-scroll').css('opacity', 0),
                    $scrollKnob: this.$('.product-view-description-scroll-knob')
                },
                options: {
                    scrollingX: false
                }
            });

            // $('.bxslider').bxSlider({
            //     buildPager: function () {
            //         return '<div class="product-view-pager"></div>';
            //     },
            //     onSliderLoad: function () {

            //     }
            // });

            that.$el.css({opacity: 0.0, scale: 2.0}).animate({
                        opacity: 1.0,
                        scale: 1.0
                    }, {
                        complete: done
                    });

        },
        _close: function (done) {
            this.scroller.destroy();
            this.$el.animate({
                opacity: 0.0,
                scale: 2.0
            }, {
                complete: done
            });
        },
        back: function () {
            window.history.back();
        }
    });
});

