/*jslint nomen:true*/
/*global define:false*/

define('views/MainScreenWidgetView',[
    'framework/View',
    'framework/Container',
    'views/MainScreenWidgetViewTab',
    'views/LanguageWidgetView',
    'views/CurrencyWidgetView',
    'views/MapView',
    'views/WeatherWidgetView',
    'templates/main-screen-widget-view'
], function (View, Container, MainScreenWidgetViewTab, LanguageWidgetView, CurrencyWidgetView, MapView, WeatherWidgetView, template) {

    'use strict';

    return View.extend({
        className: 'main-screen-widget-view',
        i18m: true,
        template: template,
        events: {
            'click .close-widget-btn': 'showDefault'
        },
        regions: {
            header: '.main-screen-widget-view-header',
            content: '.main-screen-widget-view-content'
        },
        subviews: {
            tab: MainScreenWidgetViewTab,
            container: Container
        },
        layout: {
            content: 'container',
            header: 'tab'
        },
        select: function (tab) {
            this.subviews.tab.select(tab);
        },
        initialize: function () {
            this.listenTo(this.subviews.tab, 'select', this.showTab);
            this.widgets = {
                language: new LanguageWidgetView(),
                currency: new CurrencyWidgetView(),
                map: new MapView(),
                weather: new WeatherWidgetView()
            };
        },
        showTab: function (item) {
            this.subviews.container.show(this.widgets[item]);
        },
        showDefault: function () {
            this.showTab('language');
        },
        afterRender: function () {
            this.showDefault();
        }
    });
});

