define('views/SearchResultsView',[
    'jquery',
    'framework/View',
    'templates/subcategory-view',
    'framework/utils/backend',
    'framework/utils/i18n',
    'framework/utils/log'
], function ($, View, template, backend, i18n, log) {
    'use strict';

    var pageHeight = 615;

    // TODO сейчас это копи-пейст
    // нужно сделать его отдельным видом (минимум)
    // или наследовать общий код (максимум)
    return View.extend({
        className: 'subcategory-view',
        template: template,
        i18n: true,
        events: {
            'click .back-button': 'back',
            'click .subcategory-view-next-page-button': 'next',
            'click .subcategory-view-previous-page-button': 'previous',
            'click .subcategory-view-list-a': 'sendStatistics'
        },
        initialize: function (options) {
            options = options || {};
            this.currentPage = options.page || 0;
            this._query = options.query;

            this.$el.css({opacity: 0, scale: 2.0});
        },
        sendStatistics: function (e) {
            this.logEvent(e);
            log.event('search:product', {
                id: $(e.currentTarget).data('id'),
                query: this._query
            });
        },
        _load: function (done) {
            var view = this;

            function onSuccess(results) {
                view._results = results;
                done();
            }

            $.getJSON(backend('products'), { q : this._query, lang : Firefly.lang() })
                .done(onSuccess);
            // TODO handle errors
        },
        data: function () {
            return {
                name : i18n('Search'),
                products : this._results
            };
        },
        afterRender: function () {
            this.$('.subcategory-view-list').css({'top': 0});

            if (!this.currentPage) {
                this.currentPage = 0;
            }

            var productsCount = this._results ? this._results.length : 0;

            if (productsCount > 4) {
                this.pages = productsCount % 4 === 0 ? productsCount / 4 : Math.floor(productsCount / 4) + 1;
                this.$('.subcategory-view-scroll-knob').css({'height': 600 / this.pages, top: 0});
                this.scrollStep = 600 / this.pages;
            } else {
                this.$('.subcategory-view-scroll').hide();
                this.$('.subcategory-view-next-page-button').hide();
                this.$('.subcategory-view-previous-page-button').hide();
            }

            if (this._results) {
                this.pages = this._results.length % 4 === 0 ? this._results.length / 4 : Math.floor(this._results.length / 4) + 1;
                this.$('.subcategory-view-scroll-knob').css({'height': 600 / this.pages, top: 0});
                this.scrollStep = 600 / this.pages;
            }

            this.goToPageStatic(this.currentPage);
        },
        back: function () {
            window.history.back();
        },
        _open: function (done) {
            this.$el.animate({
                opacity: 1.0,
                scale: 1.0
            }, {
                complete: done
            });
        },
        _close: function (done) {
            this.$el.animate({
                opacity: 0.0,
                scale: 2.0
            }, {
                complete: done
            });
        },
        goToPageStatic: function (page) {
            this.$('.subcategory-view-list').css({'top': -pageHeight * page});
            this.$('.subcategory-view-scroll-knob').css({'top': this.scrollStep * page});
            this.currentPage = page;
        },
        goToPage: function (page) {
            this.$('.subcategory-view-list').animate({'top': -pageHeight * page});
            this.$('.subcategory-view-scroll-knob').animate({'top': this.scrollStep * page});
            this.currentPage = page;
        },
        next: function () {
            if (this.currentPage < this.pages - 1) {
                window.Firefly.router.navigate('main/search/' + encodeURI(this._query) + '/' + (this.currentPage + 1), {trigger: true, replace: true});
            }
        },
        previous: function () {
            if (this.currentPage > 0) {
                window.Firefly.router.navigate('main/search/' + encodeURI(this._query) + '/' + (this.currentPage - 1), {trigger: true, replace: true});
            }
        }
    });
});

