define('framework/Viewport',[
    'framework/Container',
    'framework/utils/log'
], function (Container, log) {
    'use strict';

    return Container.extend({
        className: 'viewport',
        events: {
            'mousedown': 'touch'
        },
        transition: 'slide',
        initialize: function () {
            log.trace('Viewport initialized');
            this.modal = null;
            this.message = null;
            this.onMessageMousedown = this.onMessageMousedown.bind(this);
        },
        onMessageMousedown: function (e) {
            this.trigger('touch');
            e.stopPropagation();
        },
        showMessage: function (view, callback) {
            var viewport = this;
            function show () {
                viewport.message = view;
                view.$el.on('mousedown', viewport.onMessageMousedown).appendTo(viewport.$el);
                viewport.currentView.$el.addClass('blockElement');
                view.open(callback);
            }

            if (this.message) {
                this.hideMessage(show);
            } else {
                show();
            }
        },
        hideMessage: function (callback) {
            var viewport = this;

            if (!this.message) {
                if (callback) {
                    // TODO setImmediate
                    viewport.currentView.$el.removeClass('blockElement');
                    callback();
                }

                return;
            }

            this.message.close(function () {
                viewport.message.$el.off('mousedown', viewport.onMessageMousedown);
                viewport.message.$el.detach();
                if (!viewport.modal) {
                    viewport.currentView.$el.removeClass('blockElement');
                }
                if (callback) {
                    callback();
                }
            });
        },
        showModal: function (view, callback) {
            var viewport = this;

            if (this.modal) {
                this.hideModal();
            }
            view.$el.on('mousedown', function (e) {
                viewport.trigger('touch');
                e.stopPropagation();
            }).appendTo(this.$el);

            view.open(callback);

            this.modal = view;
        },
        hideModal: function (callback) {
            var modal = this.modal;
            this.modal = null;

            if (modal) {
                modal.close(function () {
                    modal.$el.detach();
                    if (callback) {
                        callback();
                    }
                });
            }
            this.$('.main-screen-content .view.container').css('display', 'block').animate({opacity: 1});
        },
        touch: function () {
            this.hideModal();
            this.trigger('touch');
        }
    });
});

