define('models/ticketCart',[
    'underscore',
    'backbone'
], function (_, Backbone) {
    'use strict';

    var ChosenTicketsCollection = Backbone.Collection.extend({
        url: 'a',
        model: Backbone.Model.extend({
            url: 'a',
            defaults: {
                amount: 1
            },
            validate: function () {
            },
            initialize: function () {
            },
            incrementAmount: function () {
                var amount = this.get('amount');
                this.set('amount', amount + 1);
            },
            decrementAmount: function () {
                var amount = this.get('amount');
                this.set('amount', --amount);
                
                if (amount === 0) {
                    this.collection.remove(this);
                }
            }
        })
    });

    /*
    Cart has a model with data about event
    ticketsWithSeats - CHOSEN tickets with seats
    ticketsWithoutSeats - CHOSEN tickets without seats
    addTicket - adds ticket to the ticketsWithSeats or ticketsWithoutSeats depending on the type ticket
    one try to add. If ticket with this id is already in cart, then the amound is simply incremented by 1.
    Otherwise it will add new instance of this ticket.
     */
    
    return {
        CONSTRAINT: 6,
        model: {
            //mock data
            //remove after development
            toJSON: function () {
                return {
                    'id': 2,
                    'name': 'ЗВЕЗДЫ ДЖАЗА',
                    'date': '2013-12-04T13:53:03.026Z', //iso8601
                    'images': [
                        '1386943911358.jpg' // картинка доступна по url `/images/1383921685710.jpg`
                    ],
                    'descriptions': 'Нильс ЛАНДГРЕН Квартет и Камерный оркестр – это мировая премьера новой джазо-симфонической программы, которая состоится 28 февраля 2014 года в Светлановском зале Московского международного Дома музыки. В составе квартета всемирно-известные джазовые музыканты – Нильс ЛАНДГРЕН (Швеция), Ларс ДАНИЕЛЬССОН (Швеция), Себастьян СТУДНИЦКИЙ (Германия), Рани КРИЖА (Марокко). Выступление именитых джазовых музыкантов с оркестром вызывают большой интерес на международной сцене. Музыканты дают возможность по новому услышать полюбившиеся публике джазовые стандарты и популярные песни в джазово-симфоническом исполнении. Этот концерт пройдет в рамках международного проекта “ЗВЕЗДЫ JAZZ’A” благодаря сотрудничеству дирекции Московского международного Дома музыки и концертного агентства “Бон Фиеста”. Узнать больше: Купить билеты на концерт Звезды Джаза в Московский международный Дом музыки. Стоимость билетов на концерт Звезды Джаза в Москве без наценки! ',
                    'fields': [ // Произвольные поля, дизайном предусмотрены только режиссер и актер, но их может и не быть
                        {
                            'name': 'Режиссер',
                            'value': 'Имя режиссера'
                        },
                        {
                            'name': 'Актеры',
                            'value': 'Список актеров'
                        }
                    ],
                    'venue': {
                        'name': 'ММДМ',
                        'address': 'Космодамианская набережная, дом 52, строение 8, м. Павелецкая',
                        'image': 'ММDM_1.jpg',
                        'seating_plan_image': '1348743538552.jpg'
                    },
                    ticketsWithSeats: [
                        {
                            'id': 2,
                            'type': 'Партер',
                            'min_price': 100,
                            'max_price': 1000,
                            'available': 10,
                            'seats': true
                        },
                        {
                            'id': 2,
                            'type': 'Партер',
                            'min_price': 100,
                            'max_price': 1000,
                            'available': 10,
                            'seats': true
                        }
                    ],
                    ticketsWithoutSeats: [
                        {
                            'id': 1,
                            'type': 'Входной билет!',
                            'min_price': 1000,
                            'max_price': 1000,
                            'available': 40,
                            'seats': false //без мест
                        },
                        {
                            'id': 2,
                            'type': 'Партер',
                            'min_price': 100,
                            'max_price': 1000,
                            'available': 10,
                            'seats': true
                        }
                    ]
                };
            }
        },
        initialize: function (model) {
            this.initialized = true;
            this.model = new Backbone.Model();
            // adding chosen tickets
            // thus only ticketsWithSeats and ticketsWithoutSeats are chosen.
            this.model.set({
                ticketsWithSeats: new ChosenTicketsCollection(),
                ticketsWithoutSeats: new ChosenTicketsCollection(),
                eventInfo: model.toJSON()
            });
            _.bindAll(this, 'addTicket', 'rmTicket');
            
            this.model.toJSON = function () {
                // converts all properties to json
                var modelJSON = Backbone.Model.prototype.toJSON.call(this),
                    result = {},
                    keys = Object.keys(modelJSON);

                keys.forEach(function (key) {
                    result[key] = modelJSON[key].toJSON ? modelJSON[key].toJSON() : modelJSON[key];
                });

                return result;
            };
        },
        addTicket: function (ticketModel) {
            var ticket = this.get(ticketModel);

            if (ticket) {
                ticket.incrementAmount();
            } else {
                this.getSuitableChosenTickets(ticketModel).add(ticketModel.toJSON());
            }
            
            this.model.trigger('addTicket', ticketModel);
            

        },

        rmTicket: function (ticketModel, silent) {
            this.get(ticketModel).decrementAmount();
            
            if (!silent) {
                this.model.trigger('rmTicket', ticketModel);
            }
        },
        calculateTotalSum: function () {
            var total = 0;

            this.model.get('ticketsWithSeats').forEach(function (model) {
                calculateProduct(model);
            });

            this.model.get('ticketsWithoutSeats').forEach(function (model) {
                calculateProduct(model);
            });

            function calculateProduct(model) {
                total += (model.get('max_price') || model.get('price')) * model.get('amount');
            }
            this.model.set('total', total);
            return total;
        },
        calculateTotalTickets: function () {
            // return total amount of tickets

            var amount = 0;
            this.model.get('ticketsWithoutSeats').forEach(function (ticketModel) {
                amount += ticketModel.get('amount');
            });
            this.ticketsWithoutSeatsN = amount;
            this.model.get('ticketsWithSeats').forEach(function (ticketModel) {
                amount += ticketModel.get('amount');
            });
            this.ticketsWithSeatsN = amount - this.ticketsWithoutSeatsN;
            return amount;
        },
        isEmpty: function () {
            return this.calculateTotalTickets() === 0;
        },
        get: function (model) {
            return this.getSuitableChosenTickets(model).get(model.id);
        },
        getById: function (id, seatId) {
            
            if (typeof seatId === 'undefined') {
                return this.model.get('ticketsWithoutSeats').get(id);
            }
            
            return this.model.get('ticketsWithSeats').get(seatId);
            
        },
        getByCategoryId: function (categoryId) {
            return this.model.get('ticketsWithSeats').where({categoryId: categoryId}) || [];
        },
        getSuitableChosenTickets: function (model) {
            return this.model.get(model.get('seats') === false ? 'ticketsWithoutSeats'  : 'ticketsWithSeats');
        },

        getSuitableChosenTicketsAmount: function (model) {
            return this.getSuitableChosenTickets(model).filter(this.filter).length;
        },
        format: function () {
            var result = {
                'event_id': this.model.get('eventInfo').id,
                tickets: []
            };

            this.model.get('ticketsWithoutSeats').forEach(function (ticketModel) {
                result.tickets.push({
                    id: ticketModel.get('id'),
                    amount: ticketModel.get('amount'),
                    price: ticketModel.get('max_price')
                });
            });

            this.model.get('ticketsWithSeats').forEach(function (ticketModel) {
                result.tickets.push({
                    'seat_id': ticketModel.get('id'),
                    id: ticketModel.get('categoryId')
                });
            });
            
            return result;
        },
        formatStatistics: function () {
            var result = {
                'event_id': undefined,
                'subevent_id': this.model.get('eventInfo').id,
                'order_id': undefined,
                'event_name': this.model.get('eventInfo').name,
                items: []
            };
            this.model.get('ticketsWithoutSeats').forEach(function (ticketModel) {
                result.items.push({
                    name: ticketModel.get('type'),
                    price: ticketModel.get('max_price'),
                    quantity: ticketModel.get('amount')
                });
            });
            this.model.get('ticketsWithSeats').forEach(function (ticketModel) {
                result.items.push({
                    name: ticketModel.get('category'),
                    price: ticketModel.get('price'),
                    quantity: ticketModel.get('amount'),
                    row: ticketModel.get('row'),
                    seat: ticketModel.get('number')
                });
            });
            return result;
        },
        destroy: function () {
            this.initialized = false;
            this.model = {};
        },
    };
});

