define('framework/View',[
    'underscore',
    'backbone',
    'framework/utils/log',
    'framework/utils/async'
], function (_, Backbone, log, async) {
    'use strict';

    /**
    * Works just like _.result, but accepts actual property, not it's name
    * so it's possible to call function in other context
    */
    function result(context, value) {
        return _.isFunction(value) ? value.call(context) : value;
    }

    var application,
        View = Backbone.View.extend({
        className: 'view',
        subviews: {},
        regions: {},
        layout: {},
        openLoading: false,
        i18n: false,

        /**
         * @return array of css class names
         */
        classNames: function () {
            var classNames = [],
                obj = Object.getPrototypeOf(this);

            while (obj !== Backbone.View.prototype) {
                if (obj.hasOwnProperty('className')) {
                    classNames.unshift(result(this, obj.className));
                }
                obj = Object.getPrototypeOf(obj);
            }

            return classNames;
        },
        /**
         * Overrides Backbone's implementation to use classNames instead of className
         */
        _ensureElement: function () {
            if (!this.el) {
                var attrs = _.extend({}, _.result(this, 'attributes')),
                    $el;

                if (this.id) {
                    attrs.id = _.result(this, 'id');
                }

                if (this.className) {
                    attrs['class'] = this.classNames().join(' ');
                }
                
                $el = Backbone.$('<' + _.result(this, 'tagName') + '>').attr(attrs);
                this.setElement($el, false);

            } else {
                this.setElement(_.result(this, 'el'), false);
            }
        },
        /**
         * Generates and assignes id to element, based on cid and className
         * @return {[type]}
         */
        id: function () {
            var classes = this.className.split(' ');

            return classes[classes.length - 1] + '-' + this.cid;
        },
        initialize: function () {
            log.trace(_.result(this, 'id') + ' initialize');
            this._state = 'unloaded';
            this._rendered = false;
            this._regions = this.regions;
            this._initializeSubviews();
            this._stateHistory = [this._state];
            this._loadingAborted = false;

            var view = this;
            if (this.i18n) {
                this.listenTo(application, 'application:languagechange', function () {
                    view.load();
                });
            }
        },
        _initializeRegions: function () {
                var regions = this._regions,
                    name;

                this.regions = {};

                for (name in regions) {
                    if (regions.hasOwnProperty(name)) {
                        this.regions[name] = this.$(regions[name]);
                    }
                }
            },
        _initializeSubviews: function () {
            var subviews = this.subviews,
                name;

            this.subviews = {};

            for (name in subviews) {
                if (subviews.hasOwnProperty(name) && _.isFunction(subviews[name])) {
                    if (subviews[name].prototype instanceof View) { // use function as a constructor
                        this.subviews[name] = new subviews[name]();
                    } else { //use function as a factory
                        this.subviews[name] = subviews[name].apply(this);
                    }
                }
            }
        },
        data: function () {
            var model = this.model || this.collection;

            return model ? model.toJSON() : {};
        },
        render: function () {
            if (this.template) {
                this.$el.html(this.template(_.result(this, 'data')));
            }

            this._initializeRegions();

            _.each(this.layout, function (subview, region) {
                this.regions[region].append(this.subviews[subview].$el);
            }, this);

            this.delegateEvents();

            this.afterRender();

            return this;
        },
        /**
         * Default implementation. Override to do something after default render()
         */
        afterRender: function () {
        },
        state : function (state) {
            if (typeof state === 'undefined') {
                return this._state;
            }

            if (state === this._state) {
                return this;
            }

            this._state = state;
            this._stateHistory.push(state);
            this.trigger('state:' + state);
        },
        logStateHistory : function () {
            log.trace(_.result(this, 'id') + ' ' + this._stateHistory.join(' → '));
        },
        load : function (callback, context, recursive) {
            var view = this;

            this._loadingAborted = false;

            if (!recursive) {
                log.trace(_.result(this, 'id') + ' load from state: ' + this.state());
            }

            if (this.state() === 'unloaded' || this.state() === 'closed') {
                this.state('loading');

                view._rendered = false;

                this._load(function () {
                    if (view._loadingAborted) {
                        return;
                    }

                    view.state('closed');

                    if (callback) {
                        callback.apply(context);
                    }
                });

                return;
            }

            if (this.state() === 'open') {
                if (this.openLoading) {
                    this.state('loading');

                    this._load(function () {
                        // TODO handle aborted loading
                        view.state('open');

                        if (callback) {
                            callback.apply(context);
                        }
                    });
                } else {
                    this.close(function () {
                        view.load(function () {
                            view.open(callback, context);
                        });
                    });
                }
             
                return;
            }

            if (this.state() === 'opening') {
                this.once('state:open', function () {
                    view.load(callback, context);
                });

                return;
            }

            this.close(function () {
                view.load(callback, context);
            }, context, true);
        },
        open : function (callback, context, recursive) {
            var view = this;

            if (!recursive) {
                log.trace(_.result(this, 'id') + ' open from state: ' + this.state());
            }


            if (this.state() === 'open') {
                if (callback) {
                    callback.apply(context);
                }

                return;
            }

            if (this.state() === 'closed') {
                this.state('opening');

                if (!this._rendered) {
                    this.render();
                    this._rendered = true;
                }

                this._open(function () {
                    view.state('open');

                    if (callback) {
                        callback.apply(context);
                    }
                });

                return;
            }

            if (this.state() === 'opening') {
                this.once('state:open', function () {
                    if (callback) {
                        callback.apply(context);
                    }
                });

                return;
            }

            this.close(function () {
                view.open(callback, context, true);
            }, context, true);
        },
        close : function (callback, context, recursive) {
            var view = this;

            if (!recursive) {
                log.trace(_.result(this, 'id') + ' close from state: ' + this.state());
            }

            if (this.state() === 'closed') {
                if (callback) {
                    callback.apply(context);
                }

                return;
            }

            if (this.state() === 'open') {
                this.state('closing');

                this._close(function () {
                    view.state('closed');

                    if (callback) {
                        callback.apply(context);
                    }
                });

                return;
            }

            if (this.state() === 'unloaded') {
                this.load(callback, context, true);

                return;
            }

            if (this.state() === 'loading') {
                this._loadingAborted = true;
                this.state('unloaded'); // TODO handle open loading

                if (callback) {
                    callback.apply(context);
                }

                return;
            }

            if (this.state() === 'closing') {
                this.once('state:closed', function () {
                    if (callback) {
                        callback.apply(context);
                    }
                });

                return;
            }

            this.open(function () {
                view.close(callback, context, true);
            }, context, true);
        },
        openSubviews : function (done) {
            this._performMethodOnSubviews('open', done);
        },
        closeSubviews : function (done) {
            this._performMethodOnSubviews('close', done);
        },
        loadSubviews : function (done) {
            this._performMethodOnSubviews('load', done);
        },
        _load : function (done) {
            done();
        },
        _open : function (done) {
            this.openSubviews(done);
        },
        _close : function (done) {
            this.closeSubviews(done);
        },
        _performMethodOnSubviews : function (method, done) {
            var keys = Object.keys(this.subviews);

            async.parallel(keys.map(function (key) {
                return this.subviews[key][method].bind(this.subviews[key]);
            }, this), done);
        },
        logEvent: function (e) {
            log.sendUserActivity(e);
        }
    });

    View.extend = function (properties, statics) {
        var oldInitialize = properties.initialize,
            that = this;

        if (properties && properties.hasOwnProperty('initialize') && _.isFunction(properties.initialize)) {
            properties.initialize = function () {
                that.prototype.initialize.apply(this, arguments);
                oldInitialize.apply(this, arguments);
            };
        } else {
            properties.initialize = function () {
                that.prototype.initialize.apply(this, arguments);
            };
        }

        if (properties && properties.hasOwnProperty('mixins') && _.isArray(properties.mixins)) {
            _.each(properties.mixins, function (mixin) {
                _.each(mixin, function (property, key) {
                    if (key === 'initialize') {
                        var oldInitialize = properties.initialize;

                        properties.initialize = function () {
                            oldInitialize.apply(this, arguments);
                            property.apply(this, arguments);
                        };
                    } else {
                        properties[key] = property;
                    }
                });
            });
        }

        return Backbone.View.extend.call(this, properties, statics);
    };

    View.init = function (app) {
        application = app;
    };

    return View;
});
