define('views/TicketSuccessView',[
    'underscore',
    'jquery',
    'framework/View',
    'models/Product',
    'templates/ticket-success-view',
    'models/ticketCart'
], function (_, $, View, Product, template, ticketCart) {
    'use strict';
    return View.extend({
        className: 'ticket-view',
        template: template,
        events: {
            'click .ticket-confirm-view-back': 'back'
        },
        initialize: function () {
            this.$el.css({opacity: 0.0, scale: 2.0});
        },
        _open: function (done) {
            var coords = this.model.options.qiwiCoords;

            if (Array.isArray(coords) && coords.length > 1) {
                window.Firefly.trigger('map:locations:ticket', coords);
            }

            this.$el.animate({
                opacity: 1.0,
                scale: 1
            }, {
                complete: done
            });
        },
        _close: function (done) {
            this.$el.animate({
                opacity: 0.0,
                scale: 2
            }, {
                complete: done
            });
        },
        _load: function (done) {
            var date = new Date(ticketCart.model.get('ttl')),
                dateObj = {
                    day: date.getDate(),
                    month: date.getMonth() + 1,
                    year: date.getFullYear(),
                    hours: date.getHours(),
                    minutes: date.getMinutes()
                };

            dateObj.day = (dateObj.day < 10 ? '0' : '') + dateObj.day;
            dateObj.month = (dateObj.month < 10 ? '0' : '') + dateObj.month;
            dateObj.minutes = (dateObj.minutes < 10 ? '0' : '') + dateObj.minutes;

            this.model = {
                options: {
                    qiwiAddress: Firefly.config('qiwiAddress'),
                    qiwiCoords: Firefly.config('qiwiCoords'),
                    paymentDate: dateObj
                },
                toJSON: function () {
                    return this.options;
                }
            };

            done();
        },
        back: function () {
            window.history.back();
        }
    });
});

