define('views/MapView',[
    'underscore',
    'jquery',
    'leaflet',
    'framework/View',
    'views/ModalView',
    'views/MetroView',
    'models/Location',
    'models/City',
    'framework/utils/backend',
    'framework/utils/leaflet'
], function (_, $, L, View, ModalView, MetroView, Location, City, backend, l) {
    'use strict';

    var MapView,
        location = new Location(),
        city = new City(),
        icons = {
            en : L.icon({
                iconUrl : '/images/marker_en.png',
                iconSize : [145, 101],
                iconAnchor : [42, 80]
            }),
            ru : L.icon({
                iconUrl : '/images/marker_ru.png',
                iconSize : [145, 101],
                iconAnchor : [42, 80]
            }),
            de : L.icon({
                iconUrl : '/images/marker_de.png',
                iconSize : [145, 101],
                iconAnchor : [42, 80]
            })
        };


    MapView = View.extend({
        className : 'map-view',
        i18n : true,
        events : {
            'click .zoom-in-btn' : 'zoomIn',
            'click .zoom-out-btn' : 'zoomOut',
            'click .traffic-btn' : 'toggleTraffic',
            'click .open-btn' : 'showLargeMap',
            'click .metro-btn' : 'showMetro',
            'click .close-btn' : 'triggerClose'
        },
        initialize : function (options) {
            options = options || {};

            this.$el.css('opacity', 0.0);

            this.layers = {
                yandex : L.tileLayer('http://vec04.maps.yandex.net/tiles?l=map&v=2.42.0&x={x}&y={y}&z={z}&lang=ru_RU')
            };

            this.marks = {
                locations : []
            };

            if (!options || !options.hasOwnProperty('close')) {
                this.options.close = false;
            }

            this.map = new L.Map(this.el, {
                crs : l.YANDEX_EPSG3395,
                layers : [this.layers.yandex],
                center : new L.LatLng(52.603584, 39.596017),
                zoom : 11,
                zoomControl : false,
                attributionControl : false,
                keyboard : false, //fixes first touch issue
                touchZoom : false
            });

            this.map.on('mousedown', function () { //TODO check if this is still relevant
                window.Firefly.viewport.trigger('touch');
            });

            this._attribution = L.control.attribution({
                position : 'bottomleft',
                prefix : false
            });

            this._attribution.addTo(this.map);

            if (!options.large) {

                this.metro = new MetroView();
                this.large = new MapView({
                    large: true
                });

                this.large.on('close', function () {
                    window.Firefly.viewport.hideModal();
                });
            }

            setTimeout(function () {
                location.set({
                    address : 'address',
                    latitude : 52.603584,
                    longitude : 39.596017
                });
            }, 100);


            this.listenTo(location, 'change', this.addKioskMarker);
            this.listenTo(city, 'change', this.initMapCenter);
            this.listenTo(window.Firefly, 'session:end', this.initMapCenter);
            this.listenTo(window.Firefly, 'map:locations:product', this.updateLocations);
            this.listenTo(window.Firefly, 'map:locations:category', this.updateLocations);
            this.listenTo(window.Firefly, 'map:locations:ticket', this.showTicketLocation);
            this.listenTo(window.Firefly, 'map:locations:clear', this.clearLocations);
        },
        initMapCenter : function () {
            var zoom = 11,
                latitude = city.get('latitude'),
                longitude = city.get('longitude');

            if (latitude && longitude) {
                this.map.setView(new L.LatLng(latitude, longitude), zoom);
            }
        },
        afterRender : function () {
            if (!this._init) {
                this.addControls();
                this._init = true;
            }

            var map = this.map;

            this.$el.on('DOMNodeInsertedIntoDocument', function (e) {
                if (e.target === e.currentTarget) {
                    map.invalidateSize();
                }
            });

            this.addKioskMarker();
        },
        addKioskMarker : function () {
            if (this.marks.kiosk) {
                this.map.removeLayer(this.marks.kiosk);
            }

            if (location.get('latitude') && location.get('longitude')) {
                this.marks.kiosk = L.marker([location.get('latitude'), location.get('longitude')], {icon : icons[window.Firefly.lang()]}).addTo(this.map);
            }
        },
        clearLocations : function () {
            var map = this.map;
            _.each(this.marks.locations, function (marker) {
                map.removeLayer(marker);
            });
        },
        updateLocations : function (locations) {
            this.clearLocations();

            _.each(locations, function (location) {
                this.marks.locations.push(L.marker([location.latitude, location.longitude]).addTo(this.map));
            }, this);

            if (locations.length > 0) {
                this.map.panTo(new L.LatLng(locations[0].latitude, locations[0].longitude));
            }
        },
        showTicketLocation : function (location) {
            this.clearLocations();
            this.marks.locations.push(L.marker(location).addTo(this.map));
            this.map.panTo(location);
        },
        addControls : function () {
            // this.$el.append('<button class="traffic-btn map-btn"></button>');
            this.$el.append('<button class="zoom-in-btn map-btn"></button><button class="zoom-out-btn map-btn"></button>');

            // if (this.options.large) {
            //     this.$el.append('<button class="close-btn map-btn"></button>');
            // } else {
            //     this.$el.append('<button class="open-btn map-btn"></button>');
            //     this.$el.append('<button class="metro-btn map-btn"></button>');
            // }
        },
        zoomIn : function () {
            this.map.zoomIn();
        },
        zoomOut : function () {
            this.map.zoomOut();
        },
        _open: function (done) {
            this.map.invalidateSize();
            this.$el.animate({
                opacity: 1.0
            }, {
                complete: done
            });
        },
        _close: function (done) {
            this.$el.animate({
                opacity: 0.0
            }, {
                complete: done
            });
        },
        toggleTraffic : function () {
            if (this.traffic) {
                this.traffic = false;
                this.map.removeLayer(this.layers.traffic);
            } else {
                this.traffic = true;
                this.map.addLayer(this.layers.traffic);
            }
        },
        showLargeMap : function () {
            window.Firefly.viewport.showModal(new ModalView({map : this.large, metro : this.metro}).city());
        },
        triggerClose : function () {
            this.trigger('close');
        },
        showMetro : function () {
            window.Firefly.viewport.showModal(new ModalView({map : this.large, metro : this.metro}).metro());
        }
    });

    return MapView;
});

