define('views/CafeView',[
    'underscore',
    'jquery',
    'framework/View',
    'models/Product',
    'templates/product-view',
    'framework/utils/Scroll',
    'framework/utils/backend',
    'framework/utils/log',

    'bxslider'
], function (_, $, View, Product, template, Scroll, backend, log) {
    'use strict';

    return View.extend({
        className: 'product-view',
        template: template,
        i18n: true,
        events: {
            'click .back-button': 'back'
        },
        initialize: function () {
            this.$el.css({opacity: 0.0});
        },
        afterRender: function () {
            var images = $('ul.product-view-slider img'),
                i;

            function onLoad() {
                /*jshint validthis:true */
                var containerAspectRatio = $(this).parent().width() / $(this).parent().height(),
                    imageAspectRatio = this.width / this.height;

                if (containerAspectRatio < imageAspectRatio) {
                    $(this).css('width', '100%');
                    $(this).css('height', 'auto');
                }
                else {
                    $(this).css('width', 'auto');
                    $(this).css('height', '100%');
                }
            }

            for (i = 0; i < images.length; i++) {
                images[i].onload = onLoad;
            }
        },
        data : function () {
            return {
                templateId : 1,
                name : 'Кафе «Корчма Гоголь»',
                description : 'Хлебосольная национальная кухня. Традиционный украинский интерьер, музыка, гостеприимство. Идеальное место для встреч с друзьями, коллегами или для воскресного обеда всей семьей с детворой и бабушками. Комфортно будет всем. Корчма расположена на пересечении Космонавтов и Терешковой, в центре города, недалеко от сквера Маркова и Быханова сада. Напротив корчмы находится дворец спорта «Звездный».Просторный, светлый зал, слегка стилизованный под украинский колорит. На стене цитаты из Гоголя. У входа, на радость детворе, стоит декоративный колодец.На столах из тёмного дерева светильники. В целом обстановка позитивная. Обслуживание ненавязчивое.',
                locations : [
                    {
                        address : ' г. Липецк, ул. Космонавтов, 24',
                        // latitude : 37.617671,
                        // longitude : 55.755768
                    }
                ],
                workTime : 'с 11 до 24',
                phones : [
                    '+7 (4742) 550004'
                ],
                logo : '/images/no-image-logo.png',
                images : [
                    // '/images/zd_vok_1.jpg',
                    '/images/lipeck/image8.jpeg'
                ],
                site: 'http://amsterdamgroup.ru/it/gogol'
            };
        },
        _open: function (done) {
            this.printAvailable = false;
            this.printDiscountAvailable = false;

            // window.Firefly.trigger('map:locations:product', this.model.get('locations'));

            var that = this,
                $description = this.$('.product-view-description');
            // initialize scroller
            this.scroller = new Scroll($description, $description.find('article'), {
                scrollbar: {
                    $scroll: this.$('.product-view-description-scroll').css('opacity', 0),
                    $scrollKnob: this.$('.product-view-description-scroll-knob')
                },
                options: {
                    scrollingX: false
                }
            });

            // $('.bxslider').bxSlider({
            //     buildPager: function () {
            //         return '<div class="product-view-pager"></div>';
            //     },
            //     onSliderLoad: function () {

            //     }
            // });

            that.$el.css({opacity: 0.0, scale: 2.0}).animate({
                        opacity: 1.0,
                        scale: 1.0
                    }, {
                        complete: done
                    });

        },
        _close: function (done) {
            this.scroller.destroy();
            this.$el.animate({
                opacity: 0.0,
                scale: 2.0
            }, {
                complete: done
            });
        },
        back: function () {
            window.history.back();
        }
    });
});

