define('views/ShopsView',[
    'framework/View',
    'templates/shops-view'
], function (View, template) {
    'use strict';

    var pageHeight = 615;

    return View.extend({
        className: 'subcategory-view',
        template: template,
        i18n: true,
        events: {
            'click .back-button': 'back',
            // 'click .subcategory-view-next-page-button': 'next',
            // 'click .subcategory-view-previous-page-button': 'previous',
            // 'click .subcategory-view-list-a': 'logEvent'
        },
        initialize: function (options) {
            options = options || {};
            this.currentPage = options.page || 0;
            this.$el.css({opacity: 0, scale: 2.0});
        },
        name : 'Магазины',
        data: function (data) {
            return {
              name: 'Магазины',
              products: [
                {
                    "id": 7771,
                    "name": "Торгово-развлекательный центр «Европа»",
                    "shortDesc": "Благодаря удобному расположению, между двух оживленных улиц — Советской и 8-го Марта, уникальной концепции, приятной атмосфере и сильному составу арендаторов, он завоевал популярность у жителей города и стал самым посещаемым и любимым. Сегодня это первый профессиональный торговый объект города современного формата, который задает высокую планку стандартов современной розничной торговли в Центрально-Черноземном регионе Росcии",
                    "recommended": true,
                    "logo": "/images/lipeck/image22.jpeg",
                    "locations": [
                      {
                        "address": "г. Липецк, Советская, д. 66.",
                        // "latitude": 56.130904,
                        // "longitude": 40.422050
                      }
                    ]
                },
                {
                    "id": 7772,
                    "name": "орговый центр «Малибу»",
                    "shortDesc": "Хотите купить хороший товар, но не любите тратить кучу времени на поездки по магазинам? Тогда торгово-развлекательный центр «Малибу» Липецк - самое верное решение!",
                    "recommended": true,
                    "logo": "/images/lipeck/image23.jpeg",
                    "locations": [
                      {
                        "address": "г. Липецк, ул. Терешковой, д. 35б",
                        // "latitude": 56.118621,
                        // "longitude": 40.352206
                      }
                    ]
                }
              ]
    };
;
        },
        afterRender: function () {
            this.$('.subcategory-view-list').css({'top': 0});

            if (!this.currentPage) {
                this.currentPage = 0;
            }

            var productsCount = this.data().length;

            if (productsCount > 4) {
                this.pages = productsCount % 4 === 0 ? productsCount / 4 : Math.floor(productsCount / 4) + 1;
                this.$('.subcategory-view-scroll-knob').css({'height': 600 / this.pages, top: 0});
                this.scrollStep = 600 / this.pages;
            } else {
                this.$('.subcategory-view-scroll').hide();
                this.$('.subcategory-view-next-page-button').hide();
                this.$('.subcategory-view-previous-page-button').hide();
            }

            this.pages = productsCount % 4 === 0 ? productsCount / 4 : Math.floor(productsCount / 4) + 1;
            this.$('.subcategory-view-scroll-knob').css({'height': 600 / this.pages, top: 0});
            this.scrollStep = 600 / this.pages;

            this.goToPageStatic(this.currentPage);
        },
        back: function () {
            window.history.back();
        },
        _open: function (done) {
            // window.Firefly.trigger('map:locations:category', this.model.get('locations'));

            this.$el.animate({
                opacity: 1.0,
                scale: 1.0
            }, {
                complete: done
            });
        },
        _close: function (done) {
            this.$el.animate({
                opacity: 0.0,
                scale: 2.0
            }, {
                complete: done
            });
        },
        goToPageStatic: function (page) {
            this.$('.subcategory-view-list').css({'top': -pageHeight * page});
            this.$('.subcategory-view-scroll-knob').css({'top': this.scrollStep * page});
            this.currentPage = page;
        },
        goToPage: function (page) {
            this.$('.subcategory-view-list').animate({'top': -pageHeight * page});
            this.$('.subcategory-view-scroll-knob').animate({'top': this.scrollStep * page});
            this.currentPage = page;
        },
        // FIXME: GOROD-375
        // next: function () {
        //     if (this.currentPage < this.pages - 1) {
        //         var fragment = 'main/hotels/' + (this.currentPage + 1);
        //         window.history.replaceState({}, '', '#' + fragment);
        //         this.goToPage(this.currentPage + 1);
        //     }
        // },
        // previous: function () {
        //     if (this.currentPage > 0) {
        //         var fragment = 'main/hotels/' + (this.currentPage - 1);
        //         window.history.replaceState({}, '', '#' + fragment);
        //         this.goToPage(this.currentPage - 1);
        //     }
        // }
    });
});

