define('views/TheaterEventsView',[
    'framework/View',
    'templates/theater-events-view'
], function (View, template) {
    'use strict';

    var pageHeight = 615;

    return View.extend({
        className: 'subcategory-view',
        template: template,
        i18n: true,
        events: {
            'click .back-button': 'back',
            'click .subcategory-view-next-page-button': 'next',
            'click .subcategory-view-previous-page-button': 'previous',
            'click .subcategory-view-list-a': 'logEvent'
        },
        initialize: function (options) {
            options = options || {};
            this.currentPage = options.page || 0;
            this.$el.css({opacity: 0, scale: 2.0});
        },
        data: function (done) {
            return [
                {
                  "name": "007: СПЕКТР. 19 ноября.",
                  "shortDesc": "Зашифрованное послание из неизвестного источника выводит Бонда на след зловещей глобальной организации под кодовым названием СПЕКТР, в то время как М пытается спасти секретную разведывательную службу от ликвидации.",
                  "logo": "/images/lipeck/spectre.jpg",
                  "locations": [
                    {
                        "address" : 'г. Липецк, ул. Терешковой, д. 25б, ТРЦ «Малибу»',
                        // "latitude" : 56.125666,
                        // "longitude" : 40.396008
                    }
                  ]
                },
                {
                  "name": "Марсианин. 19 ноября.",
                  "shortDesc": "Марсианская миссия «Арес-3» в процессе работы была вынуждена экстренно покинуть планету из-за надвигающейся песчаной бури. Инженер и биолог Марк Уотни получил повреждение скафандра во время песчаной бури. Сотрудники миссии, посчитав его погибшим, эвакуировались с планеты, оставив Марка одного.",
                  "logo": "/images/lipeck/mars.jpg",
                  "locations": [
                    {
                        "address" : 'г. Липецк, ул. Терешковой, д. 25б, ТРЦ «Малибу»',
                        // "latitude" : 56.125666,“
                        // "longitude" : 40.396008”
                    }
                  ]
                }
              ];

        },
        afterRender: function () {
            this.$('.subcategory-view-list').css({'top': 0});

            if (!this.currentPage) {
                this.currentPage = 0;
            }

            var productsCount = this.data().length;

            if (productsCount > 4) {
                this.pages = productsCount % 4 === 0 ? productsCount / 4 : Math.floor(productsCount / 4) + 1;
                this.$('.subcategory-view-scroll-knob').css({'height': 600 / this.pages, top: 0});
                this.scrollStep = 600 / this.pages;
            } else {
                this.$('.subcategory-view-scroll').hide();
                this.$('.subcategory-view-next-page-button').hide();
                this.$('.subcategory-view-previous-page-button').hide();
            }

            this.pages = productsCount % 4 === 0 ? productsCount / 4 : Math.floor(productsCount / 4) + 1;
            this.$('.subcategory-view-scroll-knob').css({'height': 600 / this.pages, top: 0});
            this.scrollStep = 600 / this.pages;

            this.goToPageStatic(this.currentPage);
        },
        back: function () {
            window.history.back();
        },
        _open: function (done) {
            // window.Firefly.trigger('map:locations:category', this.model.get('locations'));

            this.$el.animate({
                opacity: 1.0,
                scale: 1.0
            }, {
                complete: done
            });
        },
        _close: function (done) {
            this.$el.animate({
                opacity: 0.0,
                scale: 2.0
            }, {
                complete: done
            });
        },
        goToPageStatic: function (page) {
            this.$('.subcategory-view-list').css({'top': -pageHeight * page});
            this.$('.subcategory-view-scroll-knob').css({'top': this.scrollStep * page});
            this.currentPage = page;
        },
        goToPage: function (page) {
            this.$('.subcategory-view-list').animate({'top': -pageHeight * page});
            this.$('.subcategory-view-scroll-knob').animate({'top': this.scrollStep * page});
            this.currentPage = page;
        },
        // FIXME: GOROD-375
        next: function () {
            if (this.currentPage < this.pages - 1) {
                var fragment = 'main/theater-events/' + (this.currentPage + 1);
                window.history.replaceState({}, '', '#' + fragment);
                this.goToPage(this.currentPage + 1);
            }
        },
        previous: function () {
            if (this.currentPage > 0) {
                var fragment = 'main/theater-events/' + (this.currentPage - 1);
                window.history.replaceState({}, '', '#' + fragment);
                this.goToPage(this.currentPage - 1);
            }
        }
    });
});

