define('framework/PreViewScreen',[
    'jquery',
    'framework/Screen',
    'framework/Container',
    'views/SearchView',
    'views/MainScreenWidgetView',
    'views/SubcategoryView',
    'views/ProductView',
    'views/PreView',
    'templates/main-screen'
], function ($, Screen, Container,  SearchView, MainScreenWidgetView,   SubcategoryView, ProductView, PreView, template) {
    'use strict';

    return Screen.extend({
        className : 'main-screen',
        template : template,
        routes : {
            ':id' : 'preview'
        },
        regions : {
            content : '.main-screen-content'
        },
        subviews : {
            container : Container,
            widgetView : MainScreenWidgetView,
            a: PreView,
            subcategoryView : SubcategoryView,
            productView : ProductView
        },
        layout : {

            content : 'container'
        },
        init : function () {

            if (!this.inited) {

                this.inited = true;
            }

        },
        product : function (id) {
            this.init();
            this.subviews.container.show(this.subviews.productView.reloadWithId(id));
            this.subviews.widgetView.select('map');
        },
        preview: function (id) {
            this.subviews.container.show(this.subviews.a.reloadPreview(id));
            var $el = $(this.el);
            $el.parent().css('margin-top', '-422px');
            $el.parent().css('height', '1350px');
        }
    });
});

