/*global define:false*/

define('framework/utils/backend',[],function () {
    'use strict';

    var application,
        backend = function (url) {
            var backend = application.config('backend');

            if (url === undefined) {
                return backend;
            }

            if (backend.charAt(backend.length - 1) === '/') {
                backend = backend.slice(0, backend.length - 2);
            }

            if (url.charAt(0) === '/') {
                url = url.slice(1);
            }

            return backend + '/' + url;
        };

    backend.init = function (app) {
        application = app;
    };

    return backend;
});
