define('views/MainScreenWidgetViewTab',[
    'jquery',
    'framework/View',
    'templates/main-screen-widget-view-tab'
], function ($, View, template) {
    'use strict';

    return View.extend({
        className: 'main-screen-widget-view-tab',
        template: template,
        i18n: true,
        events: {
            'click .main-screen-widget-view-tab-item': 'onItemClicked'
        },
        initialize: function () {
            this.current = null;
        },
        onItemClicked: function (e) {
            var item = $(e.currentTarget).attr('data-widget');
            this.select(item);
        },
        select: function (item) {
            if (this.current !== item) {
                this.current = item;
                this.unhighlight();
                this.highlight(item);
                this.trigger('select', item);
            }
        },
        deselect: function () {
            this.unhighlight();
            this.current = null;
        },
        afterRender: function () {
            if (this.current) {
                this.highlight(this.current);
            }
        },
        unhighlight: function () {
            this.$('.main-screen-widget-view-tab-item-highlighted').removeClass('main-screen-widget-view-tab-item-highlighted');
        },
        highlight: function (item) {
            this.$('.main-screen-widget-view-tab-item-' + item).addClass('main-screen-widget-view-tab-item-highlighted');
        }
    });
});
