define('views/StolovayaView',[
    'underscore',
    'jquery',
    'framework/View',
    'models/Product',
    'templates/product-view',
    'framework/utils/Scroll',
    'framework/utils/backend',
    'framework/utils/log',

    'bxslider'
], function (_, $, View, Product, template, Scroll, backend, log) {
    'use strict';

    return View.extend({
        className: 'product-view',
        template: template,
        i18n: true,
        events: {
            'click .back-button': 'back'
        },
        initialize: function () {
            this.$el.css({opacity: 0.0});
        },
        afterRender: function () {
            var images = $('ul.product-view-slider img'),
                i;

            function onLoad() {
                /*jshint validthis:true */
                var containerAspectRatio = $(this).parent().width() / $(this).parent().height(),
                    imageAspectRatio = this.width / this.height;

                if (containerAspectRatio < imageAspectRatio) {
                    $(this).css('width', '100%');
                    $(this).css('height', 'auto');
                }
                else {
                    $(this).css('width', 'auto');
                    $(this).css('height', '100%');
                }
            }

            for (i = 0; i < images.length; i++) {
                images[i].onload = onLoad;
            }
        },
        data : function () {
            return {
                templateId : 1,
                name : 'Кафе-столовая «Серебряная подкова»',
                description : '«Серебряная подкова» - единственное кафе-столовая в центре города. Для Вас готовят высококвалифицированные опытные повара. Все блюда готовятся только из натуральных продуктов. «Серебряная подкова» предлагает бизнес-ланчи от 120 рублей и каждый день новое меню! Кафе-столовая заключает договоры на обслуживание с организациями на выгодных условиях. Большие скидки. Серебряная подкова располагает двумя залами: на 40 и 60 посадочных мест. Любые торжества — от 700 рублей в двух залах, под живую музыку, с ведущим и большим танцполом.',
                locations : [
                    {
                        address : 'г. Владимир, ул. Гагарина, д. 5',
                        latitude : 56.127919,
                        longitude : 40.404021
                    }
                ],
                workTime : 'пн-вск 9:00-18:00.',
                phones : [
                    '+7 (4922) 42-10-87'
                ],
                logo : '/images/l205.jpg',
                images : [
                    // '/images/zd_vok_1.jpg',
                    '/images/silver_podkova.jpg'
                ]
            };
        },
        _open: function (done) {
            this.printAvailable = false;
            this.printDiscountAvailable = false;

            // window.Firefly.trigger('map:locations:product', this.model.get('locations'));

            var that = this,
                $description = this.$('.product-view-description');
            // initialize scroller
            this.scroller = new Scroll($description, $description.find('article'), {
                scrollbar: {
                    $scroll: this.$('.product-view-description-scroll').css('opacity', 0),
                    $scrollKnob: this.$('.product-view-description-scroll-knob')
                },
                options: {
                    scrollingX: false
                }
            });

            // $('.bxslider').bxSlider({
            //     buildPager: function () {
            //         return '<div class="product-view-pager"></div>';
            //     },
            //     onSliderLoad: function () {

            //     }
            // });

            that.$el.css({opacity: 0.0, scale: 2.0}).animate({
                        opacity: 1.0,
                        scale: 1.0
                    }, {
                        complete: done
                    });

        },
        _close: function (done) {
            this.scroller.destroy();
            this.$el.animate({
                opacity: 0.0,
                scale: 2.0
            }, {
                complete: done
            });
        },
        back: function () {
            window.history.back();
        }
    });
});

