define('views/TransportView',[
    'underscore',
    'jquery',
    'framework/View',
    'models/Product',
    'templates/product-view',
    'framework/utils/Scroll',
    'framework/utils/backend',
    'framework/utils/log',

    'bxslider'
], function (_, $, View, Product, template, Scroll, backend, log) {
    'use strict';

    return View.extend({
        className: 'product-view',
        template: template,
        i18n: true,
        events: {
            'click .back-button': 'back',
            // 'click .print-button': 'goToEvents'
        },
        initialize: function () {
            this.$el.css({opacity: 0.0});
        },
        afterRender: function () {
            var images = $('ul.product-view-slider img'),
                i;

            function onLoad() {
                /*jshint validthis:true */
                var containerAspectRatio = $(this).parent().width() / $(this).parent().height(),
                    imageAspectRatio = this.width / this.height;

                if (containerAspectRatio < imageAspectRatio) {
                    $(this).css('width', '100%');
                    $(this).css('height', 'auto');
                }
                else {
                    $(this).css('width', 'auto');
                    $(this).css('height', '100%');
                }
            }

            for (i = 0; i < images.length; i++) {
                images[i].onload = onLoad;
            }
        },
        data : function () {
            return {
                templateId : 1,
                name : 'Железнодорожный вокзал г. Липецк',
                description : 'На этом месте в первые годы существования Липецка как областного центра был небольшой полустанок. В начале 1960-х годов на улице Гагарина был построен двухэтажный железнодорожный вокзал в створе нынешней улицы Терешковой. Он завершил перспективу этой улицы. Однако это здание простояло около тридцати лет. В 1976 году институт «Липецкгражданпроект» предложил создать на базе железнодорожного вокзала комплекс, в который бы также входил и автовокзал. Однако из-за дефицита места для стоянки автобусов от этой идеи отказались. Автовокзал было решено строить на проспекте Победы, где он находится сейчас. Но новый железнодорожный вокзал на 1000 пассажиров тем не менее строить начали. Он вступил в строй в 1994 году. На вокзале скульпторЕ. А. Вольфсон выполнил несколько скульптур, в том числе «Начальник вокзала и обходчик», «Дама и носильщик». Другой скульптор — И. М. Мазур исполнил декоративную композицию',
                locations : [
                    {
                        address : 'г. Липецк, ул. Гагарина, д. 106',
                        // latitude : 55.755768,
                        // longitude : 37.617671
                    }
                ],
                workTime : 'круглосуточно',
                phones : [
                    '+7 (800) 775-00-00'
                ],
                logo : '/images/no-image-logo.png',
                images : [
                    // '/images/zd_vok_1.jpg',
                    '/images/lipeck/image18.jpeg'
                ]
            };
        },
        _open: function (done) {
            this.printAvailable = false;
            this.printDiscountAvailable = false;

            // window.Firefly.trigger('map:locations:product', this.model.get('locations'));

            var that = this,
                $description = this.$('.product-view-description');
            // initialize scroller
            this.scroller = new Scroll($description, $description.find('article'), {
                scrollbar: {
                    $scroll: this.$('.product-view-description-scroll').css('opacity', 0),
                    $scrollKnob: this.$('.product-view-description-scroll-knob')
                },
                options: {
                    scrollingX: false
                }
            });

            // $('.bxslider').bxSlider({
            //     buildPager: function () {
            //         return '<div class="product-view-pager"></div>';
            //     },
            //     onSliderLoad: function () {

            //     }
            // });

            that.$el.css({opacity: 0.0, scale: 2.0}).animate({
                        opacity: 1.0,
                        scale: 1.0
                    }, {
                        complete: done
                    });

        },
        _close: function (done) {
            this.scroller.destroy();
            this.$el.animate({
                opacity: 0.0,
                scale: 2.0
            }, {
                complete: done
            });
        },
        back: function () {
            window.history.back();
        },
        goToEvents: function () {
            Firefly.navigate('main/theater-events');
        }
    });
});

