requirejs.config({
    paths : {
        'backbone' : '../components/backbone/backbone',
        'underscore' : '../components/underscore/underscore',
        'jquery' : '../components/jquery/jquery',
        'handlebars' : '../components/handlebars/handlebars.runtime',
        'jquery.transit' : '../components/jquery.transit/jquery.transit',
        'jquery.easing' : '../components/jquery.easing/js/jquery.easing',
        'leaflet' : '../components/leaflet-dist/leaflet',
        'bxslider' : '../components/bxslider-4/jquery.bxslider',
        'ftscroller' : 'framework/utils/ftscroller',
        'phantom-limb' : '../components/phantom-limb/phantom-limb',
        'kiosk-session-manager' : '../components/kiosk-session-manager/dist'
    },
    shim : {
        jquery : {
            exports : 'jQuery'
        },
        'jquery.transit' : {
            deps : ['jquery']
        },
        'jquery.easing' : {
            deps : ['jquery']
        },
        underscore : {
            exports : '_'
        },
        'bxslider' : {
            deps : ['jquery']
        },
        backbone : {
            deps : ['jquery', 'underscore'],
            exports : 'Backbone'
        },
        handlebars : {
            exports : 'Handlebars'
        },
        leaflet : {
            exports : 'L'
        },
        ftscroller : {
            exports : 'FTScroller'
        }
    }
});


requirejs([
    'leaflet',
    'jquery',
    'framework/Application',
    'views/MainScreen',
    'collections/LocationCollection',
    'dictionaries/en',
    'dictionaries/ru',
    'dictionaries/de',
    'framework/PreViewScreen',
    'framework/utils/backend',
    'framework/utils/log'
], function (L, $, Application, MainScreen, LocationCollection, en, ru, de, PreViewScreen, backend, log) {
    'use strict';

    L.Icon.Default.imagePath = '/images';

    var Firefly,
        config = {},
        ENV = window.ENV || 'production';

    config.production = {
        languages : [
            'en',
            'ru',
            'de'
        ],
        defaultLanguage : 'ru',
        defaultScreen : 'main',
        backend : '/data',
        ticketBackend: window.location.protocol + '//' + window.location.hostname + ':8000',
        sessionTimeout : '1 minute 30 seconds',
        dictionaries : {
            'en' : en,
            'ru' : ru,
            'de' : de
        },
        digitImageDuration : '7.5 seconds',
        showTickets : false
    };

    config.development = {
        languages : [
            'en',
            'ru',
            'de'
        ],
        defaultLanguage : 'ru',
        defaultScreen : 'main',
        backend : '/data',
        ticketBackend: window.location.protocol + '//' + window.location.hostname + ':8000',
        sessionTimeout : '1000000 seconds',

        dictionaries : {
            'en' : en,
            'ru' : ru,
            'de' : de
        },
        digitImageDuration : '7.5 seconds',
        showTickets : true
    };

    log.info('Starting with ENV = ' + window.ENV);

    Firefly = window.Firefly = new Application(config[ENV]);

    Firefly.addScreens({
        main : new MainScreen()
    });

    $(document).keydown(function(e) {
        if (e.keyCode == 27) {
            require('nw.gui').App.quit();
        }
    });

    $(function () {
        Firefly.start();


        var width = window.innerWidth;
        var height = window.innerHeight;
        var targetWidth = 1080;
        var targetHeight = 1920;

        var xScale = width / targetWidth;
        var yScale = height / targetHeight;
        var scale = Math.min(xScale, yScale);



        // $('.viewport').css('-webkit-transform', 'scale(' + scale + ',' + scale + ')');
        // $('body').css('height', height + 'px');
    });
});

define("main", function(){});

