define('views/CultureView',[
    'jquery',
    'framework/View',
    'templates/culture-view',
    'framework/utils/log',
    'framework/utils/async'
], function ($, View, template, log, async) {
    'use strict';

    var ITEMS_IN_ROW = 5,
        VERTICAL_MARGIN = 50,
        HORIZONTAL_MARGIN = 30,
        ITEM_WIDTH = 130,
        ITEM_HEIGHT = 160,
        TOTAL_DURATION = 800,
        MIN_DURATION = 150;

    return View.extend({
        className: 'category-view',
        template: template,
        i18n: true,
        events: {
            'click .back-button': 'back',
            'click .category-view-item-a': 'logEvent'
        },
        initialize: function () {
            this.$el.css({
                opacity : 0.0,
                scale : 2.0
            });
        },
        back: function () {
            window.history.back();
        },
        data: function () {
            return [
                {
                  "id": 496,
                  "name": "Художественный музей",
                  "icon": "/images/4.png",
                  url: "#main/hud"
                },
                {
                  "id": 494,
                  "name": "Краеведческий музей",
                  "icon": "/images/3.png",
                  url: "#main/kraev"
                },
                {
                  "id": 492,
                  "name": "Религиозные памятники",
                  "icon": "/images/lipeck/relig.png",
                  url: "#main/museum"
                },
                // {
                //   "id": 494,
                //   "name": "Науки и техники музей",
                //   "icon": "/images/5.png",
                //   url: "#main/tech"
                // },
                // {
                //   "id": 494,
                //   "name": "Театральный музей",
                //   "icon": "/images/6.png"
                // },
                // {
                //   "id": 494,
                //   "name": "Музыкальный музей",
                //   "icon": "/images/7.png"
                // },
                {
                  "id": 491,
                  "name": "Библиотека",
                  "icon": "/images/2.png",
                  url: "#main/culcenter"
                }
              ];

        },
        afterRender: function () {
            this._items = this.$('.category-view-list > li');

            var itemCount = this._items.length,
                rowCount = itemCount % ITEMS_IN_ROW ? Math.floor(itemCount / ITEMS_IN_ROW) + 1 : itemCount / ITEMS_IN_ROW,
                listWidth = rowCount === 1 ? itemCount * (ITEM_WIDTH + HORIZONTAL_MARGIN) - HORIZONTAL_MARGIN :
                                             ITEMS_IN_ROW * (ITEM_WIDTH + HORIZONTAL_MARGIN) - HORIZONTAL_MARGIN,
                listHeight = rowCount * (ITEM_HEIGHT + VERTICAL_MARGIN) - VERTICAL_MARGIN;

            if (itemCount) {
                this.duration = Math.max(MIN_DURATION, TOTAL_DURATION / itemCount);
            }

            log.trace('item count: '  + itemCount);
            log.trace('row count: '   + rowCount);
            log.trace('list width: '  + listWidth);
            log.trace('list height: ' + listHeight);

            this.$('.category-view-list').css({
                top: (615 - listHeight) / 2,
                left: (900 - listWidth) / 2,
                width: listWidth,
                height: listHeight
            });

            this._items.each(function (index, element) {
                var xIndex = index % ITEMS_IN_ROW,
                    yIndex = Math.floor(index / ITEMS_IN_ROW);

                log.trace('xIndex: ' + xIndex);
                log.trace('yIndex: ' + yIndex);

                $(element).css({
                    top: yIndex * (ITEM_HEIGHT + VERTICAL_MARGIN),
                    left: xIndex * (ITEM_WIDTH + HORIZONTAL_MARGIN),
                    scale: 2.0,
                    opacity: 0.0
                });
            });
        },
        _open: function (done) {
            var tasks = [],
                duration = this.duration,
                $el = this.$el;

            tasks.push(function (done) {
                $el.animate({ opacity : 1.0, scale : 1.0 }, done);
            });

            this._items.each(function (index, element) {
                duration -= 5;
                tasks.push(function (done) {
                    $(element).animate({
                        scale: 1.0,
                        opacity: 1.0
                    }, duration, done);
                });
            });

            async.serial(tasks, done);
        },
        _close: function (done) {
            var tasks = [],
                duration = this.duration,
                $el = this.$el;

            tasks.push(function (done) {
                $el.animate({ opacity : 0.0, scale : 2.0 }, done);
            });

            this._items.each(function (index, element) {
                duration -= 5;

                tasks.unshift(function (done) {
                    $(element).animate({
                        scale: 2.0,
                        opacity: 0.0
                    }, duration, done);
                });
            });

            async.serial(tasks, done);
        }
    });
});

