define('framework/Application',[
    'jquery',
    'underscore',
    'backbone',
    'kiosk-session-manager',
    'framework/Viewport',
    'framework/View',
    'framework/utils/milliseconds',
    'framework/utils/backend',
    'framework/utils/i18n',
    'framework/utils/log',
    'framework/utils/ticketBackend',
    'framework/utils/keyboard',
    'framework/utils/phantom-limb'
], function ($, _, Backbone, KioskSessionManager, Viewport, View, milliseconds, backend, i18n, log, ticketBackend, keyboard) {
    'use strict';

    var application,
        Application = function (config) {
            var that = this,
                sessionManager;

            _.extend(this._config = {}, config);

            this._lang = this.config('defaultLanguage');

            this.screens = {};

            backend.init(this);
            ticketBackend.init(this);
            i18n.init(this);
            View.init(this);

            sessionManager = this.sessionManager = new KioskSessionManager({
                timeout: milliseconds(this.config('sessionTimeout'))
            });

            sessionManager.on('start', function () {
                that.trigger('session:start');
                log.event('session:start');
                log.info('session start');
            });

            sessionManager.on('end', function () {
                that.trigger('session:end');
                that.router.navigate('', {trigger: true});
                that.lang(that.config('defaultLanguage'));
                that.viewport.hideModal();
                that.viewport.hideMessage();
                log.event('session:end');
                log.info('session end');
            });

            sessionManager.on('reset', function () {
                log.info('session reset');
                log.info(sessionManager.session.left());
            });

            this._defers = 1;

            application = this;
        },
        Router = Backbone.Router.extend({
            routes: {
                '': 'process',
                ':screen': 'process',
                ':screen/*route': 'process'
            },
            process: function (screen, route) {
                application.route(screen, route);
            }
        });

    _.extend(Application.prototype, {
        addScreens: function (screens) {
            _.each(screens, function (screen) {
                screen.render();
            });

            _.extend(this.screens, screens);
            return this;
        },
        config: function (key) {
            if (key === undefined) {
                return this._config;
            }

            return this._config[key];
        },
        lang: function (lang) {
            if (lang === undefined) {
                return this._lang;
            }

            if (this._lang === lang) {
                return lang;
            }

            this._lang = lang;
            this.trigger('application:languagechange', {language: lang});
            log.event('language:change', {language: lang});

            return lang;
        },
        start: function () {
            this.advanceReadiness();
        },
        deferReadiness: function (num) {
            if ('number' !== typeof num) {
                num = 1;
            }

            this._defers++;
        },
        advanceReadiness: function () {
            if (this._defers) {
                this._defers--;
            }

            if (this._defers) {
                return;
            }

            this.viewport = new Viewport().render();
            $('body').append(this.viewport.$el);
            keyboard.init(this.viewport.$el);

            this.viewport.on('touch', function () {
                if (application.sessionManager.session) {
                    application.sessionManager.session.reset();
                } else {
                    application.sessionManager.startSession();
                }
            });

            this.router = new Router();
            Backbone.history.start();

            this.trigger('start');
            return this;
        },
        route: function (screen, route) {
            if (screen === undefined) {
                screen = this.config('defaultScreen');
            }

            if (route === undefined) {
                route = '';
            }

            log.info('Route "' + route + '" of screen "' + screen + '" will be processed');
            this.viewport.show(this.screens[screen]);
            this.screens[screen].route(route);
        },
        navigate: function (fragment) {
            this.router.navigate(fragment, {trigger: true});
        }
    }, Backbone.Events);

    Backbone._ajax = Backbone.ajax;

    Backbone.ajax = function (options) {
        var data = options.data || {};
        data.lang = application.lang();
        options.data = data;

        return Backbone._ajax(options);
    };

    return Application;
});

