define('views/SearchView',[
    'framework/View',
    'templates/search-view'
], function (View, template) {
    'use strict';

    return View.extend({
        className: 'search-view',
        i18n: true,
        template: template,
        events: {
            'submit .search-form': 'search'
        },
        initialize : function () {
            Firefly.on('session:end', this.clear, this);
        },
        afterRender: function () {
            this.$input = this.$('input');
        },
        search: function (e) {
            e.preventDefault();
            Firefly.navigate('main/search/' + encodeURI(this.$('input').val()));
        },
        hide: function () {
            this.$el.addClass('search-view-hidden');
        },
        show: function () {
            this.$el.removeClass('search-view-hidden');
        },
        clear: function () {
            this.$input.trigger('focusout');
            this.$input.val('');
        }
    });
});
