define('views/ReastoranView',[
    'underscore',
    'jquery',
    'framework/View',
    'models/Product',
    'templates/product-view',
    'framework/utils/Scroll',
    'framework/utils/backend',
    'framework/utils/log',

    'bxslider'
], function (_, $, View, Product, template, Scroll, backend, log) {
    'use strict';

    return View.extend({
        className: 'product-view',
        template: template,
        i18n: true,
        events: {
            'click .back-button': 'back'
        },
        initialize: function () {
            this.$el.css({opacity: 0.0});
        },
        afterRender: function () {
            var images = $('ul.product-view-slider img'),
                i;

            function onLoad() {
                /*jshint validthis:true */
                var containerAspectRatio = $(this).parent().width() / $(this).parent().height(),
                    imageAspectRatio = this.width / this.height;

                if (containerAspectRatio < imageAspectRatio) {
                    $(this).css('width', '100%');
                    $(this).css('height', 'auto');
                }
                else {
                    $(this).css('width', 'auto');
                    $(this).css('height', '100%');
                }
            }

            for (i = 0; i < images.length; i++) {
                images[i].onload = onLoad;
            }
        },
        data : function () {
            return {
                templateId : 1,
                name : 'Ресторан «Амстердам»',
                description : 'Стильное заведение премиум класса. Входит в международный справочник TripAdvisor. Награжден дипломом за лучшее артистичное решение в дизайне интерьера на международном профессиональном конкурсе "Дизайн ресторана и отеля". Европейская и японская кухня. Достойная винотека. Кафе расположено в самом центре города, на улице «Пролетарская», рядом с кафе «Буфет»Это светский шарм и домашний уют, свобода и безумие страстей. Это столица Голландии в миниатюре: стилизованные улочки, крошечные оконца, ажурные мостики. Атмосфера ресторана переносит посетителя в центр дивного средневекового города, где на каждом шагу поджидают приятные неожиданности...',
                locations : [
                    {
                        address : 'г. Липецк, ул. Пролетарская, 13',
                        // latitude : 37.617671,
                        // longitude : 55.755768
                    }
                ],
                workTime : 'Пн - Чт: с 11:00 до 12:00',
                phones : [
                    '+7 (4742) 272665'
                ],
                logo : '/images/no-image-logo.png',
                images : [
                    // '/images/zd_vok_1.jpg',
                    '/images/lipeck/image7.jpeg'
                ],
                // site: 'http://stakeoff.ru'
            };
        },
        _open: function (done) {
            this.printAvailable = false;
            this.printDiscountAvailable = false;

            // window.Firefly.trigger('map:locations:product', this.model.get('locations'));

            var that = this,
                $description = this.$('.product-view-description');
            // initialize scroller
            this.scroller = new Scroll($description, $description.find('article'), {
                scrollbar: {
                    $scroll: this.$('.product-view-description-scroll').css('opacity', 0),
                    $scrollKnob: this.$('.product-view-description-scroll-knob')
                },
                options: {
                    scrollingX: false
                }
            });

            // $('.bxslider').bxSlider({
            //     buildPager: function () {
            //         return '<div class="product-view-pager"></div>';
            //     },
            //     onSliderLoad: function () {

            //     }
            // });

            that.$el.css({opacity: 0.0, scale: 2.0}).animate({
                        opacity: 1.0,
                        scale: 1.0
                    }, {
                        complete: done
                    });

        },
        _close: function (done) {
            this.scroller.destroy();
            this.$el.animate({
                opacity: 0.0,
                scale: 2.0
            }, {
                complete: done
            });
        },
        back: function () {
            window.history.back();
        }
    });
});

