define('models/Subcategory',[
    'underscore',
    'backbone',
    'framework/utils/backend'
], function (_, Backbone, backend) {
    'use strict';

    return Backbone.Model.extend({
        urlRoot: function () {
            return backend('subcategories');
        },
        parse: function (response) {
            response.locations = [];

            _.each(response.products, function (product) {
                _.each(product.locations, function (location) {
                    location.product = product;
                    response.locations.push(location);
                });
            });

            return response;
        }
    });
});
