define('views/TechMuseumView',[
    'underscore',
    'jquery',
    'framework/View',
    'templates/museum-view',
    'framework/utils/Scroll',
    'framework/utils/backend',
    'framework/utils/log',
    'views/SearchView',
    'collections/_eventCollection',
    'models/ticketCart',
    'views/TicketViewTable',
    'views/MessageView'
], function (_, $, View, template, Scroll, backend, log, SearchView, _eventCollection, ticketCart, TicketViewTable, MessageView) {
    'use strict';

    var months = ['Январь', 'Февраль', 'Март', 'Апрель', 'Май', 'Июнь', 'Июль', 'Август', 'Сентябрь', 'Октябрь', 'Ноябрь', 'Декабрь'];

    return View.extend({
        className: 'ticket-view',
        template: template,
        events: {
            'click .back-button': 'back',
            'click button.ticket-view-buy-button': 'proceed',
            'click .ticket-view-table-choice-minus': 'clickMinus',
            'click .ticket-view-table-choice-plus': 'clickPlus'
        },
        initialize: function (options) {
            options = options || {};
            this.$el.css({opacity: 0.0, scale: 2.0});
            this.deltaY = 18; // delta to scroll numbers
            this.preventScroll = 0;
        },
        afterRender: function () {

        },
        clickMinus: function (evt) {
            var $parent = $(evt.fromElement).parent();

            (function (curAmount, $el, dy) {
                if (curAmount !== 0) {
                    var $curEl = $el.find('span[data-active]');

                    if ($curEl.prev().length === 0) {
                        $el.prepend('<span style="margin-top:-18px"> ' + (curAmount - 1));
                    }

                    setTimeout(function () {
                        $el.find('span').first().css('margin', 0);
                    }, 0);

                    $curEl.removeAttr('data-active');
                    $curEl.prev().attr('data-active', curAmount - 1);
                    $el.css('transform', 'translateY(' + ($curEl.prevAll().length - 1) * (-dy) + 'px)');

                }
            })(+$parent.find('span[data-active]').attr('data-active'),
                $parent.find('.numbers-container'),
                18);
        },
        clickPlus: function (evt) {
            var $parent = $(evt.fromElement).parent();


            (function (curAmount, $el, dy) {

                if (curAmount >= $el.find('span').attr('data-max')) {
                    return;
                }
                var $curEl = $el.find('span[data-active]');
                if ($curEl.next().length === 0) {
                    $el.append('<span>' + (curAmount + 1));
                }
                $curEl.removeAttr('data-active');
                $curEl.next().attr('data-active', curAmount + 1);
                $el.css('transform', 'translateY(' + ($curEl.prevAll().length + 1) * (-dy) + 'px)');
                if ($curEl.next().length === 0) {
                    $el.append('<span>' + (curAmount + 1));
                }

            })(+$parent.find('span[data-active]').attr('data-active'),
                $parent.find('.numbers-container'),
                18);

        },
        data: function () {
          return {
                name: 'Музей железнодорожного транспорта',
                address: 'г. Владимир, ул. Вокзальная, д. 12',
                // phone: 'Тел.:8 (4922) 22-48-72',
                description: 'Музей железнодорожного транспорта города Владимира располагается рядом с вокзалом. Посетители могут ознакомиться с образцами подвижного состава, узкоколейными грузовыми и пассажирскими вагонами. Все экспонаты размещены под открытым небом. Особое место в экспозиции занимает паровоз серии «Л». Он являлся одним из лучших паровозов советской эпохи. Экспонат был прописан в городском депо и работал на участках до Орехово, Коврова, Мурома. Был построен в 1952 году. Паровоз попал в госрезерв в 1995 году, а в число экспонатов музея железнодорожного транспорта его зачислили в 1999 году. Данный паровоз является символом трудовой славы владимирского локомотивного депо. Горьковская магистраль входит в число старейших железных дорог нашей страны. Первой железнодорожной магистралью стало направление Москва – Нижний Новгород. Ее строительство началось в 1858 году. Движение поездов до Владимира открылось в 1861 году. Именно в этот год 19 марта экстренный поезд из столицы привез весть об отмене крепостного права. 14 июня был выпущен первый пассажирский поезд до Москвы. Он находился в пути 16 часов. В варианте скорого поезда данный путь требовал 13 часов. Московско-Нижегородская железная дорога была построена в 1862 году. Она связывала Поволжье с центром России и проходила по Нижегородской, Владимирской и Московской губерниям. Протяженность дороги составляло 410 верст. Именно эта магистраль выполняла важнейшие задачи снабжения продуктами и доставки товаров.',
                logo1: '/images/no-image.png',
                logo2: '/images/zd-mus.jpg',
                excursions: [{
                    name: 'Обзорная 13-17вв',
                    category: 'детский',
                    amount: '23',
                    price: 80
                }, {
                    name: 'Обзорная 13-17вв',
                    category: 'взрослый',
                    amount: '40',
                    price: 150
                }, {
                    name: 'Обзорная 13-17вв',
                    category: 'льготный',
                    amount: '10',
                    price: 70
                }, {
                    name: 'Обзорная 18-20вв',
                    category: 'детский',
                    amount: '23',
                    price: 80
                }, {
                    name: 'Обзорная 18-20вв',
                    category: 'взрослый',
                    amount: '40',
                    price: 150
                }, {
                    name: 'Обзорная 18-20вв',
                    category: 'льготный',
                    amount: '10',
                    price: 70
                }]
            };
        },

        _open: function (done) {
            var $description = this.$('.ticket-view-description'),
                self = this;
            this.scroller = new Scroll($description, $description.find('p'), {
                scrollbar: {
                    $scroll: this.$('.ticket-view-description-scroll').css('opacity', 0),
                    $scrollKnob: this.$('.ticket-view-description-scroll-knob')
                },
                callback: function (left, top) {

                    if (top > 0) {
                        $description.css('box-shadow', 'inset 0px 17px 14px -17px');
                    } else {
                        $description.css('box-shadow', 'none');
                    }

                },
                options: {
                    scrollingX: false
                }
            });


            this.scrollTable = new Scroll($('.ticket-view-table-wrapper'), $('.ticket-view-table-wrapper table'), {
                scrollbar: {
                    $scroll: this.$('.ticket-view-table-scroll').css('opacity', 0),
                    $scrollKnob: this.$('.ticket-view-table-scroll-knob')
                },
                options: {
                    scrollingX: false
                }
            });

            this.$el.animate({
                opacity: 1.0,
                scale: 1
            }, {
                complete: done
            });

        },
        _close: function (done) {
            this.scroller.destroy();
            this.scrollTable.destroy();
            this.$el.animate({
                opacity: 0.0,
                scale: 2
            }, {
                complete: done
            });

        },
        back: function () {
            window.history.back();
        },
        proceed: function () {
            Firefly.viewport.showMessage(new MessageView({
                message: 'Билеты забронированы'
            }));
        }
    });
});

