define('views/BuolingView',[
    'underscore',
    'jquery',
    'framework/View',
    'models/Product',
    'templates/product-view',
    'framework/utils/Scroll',
    'framework/utils/backend',
    'framework/utils/log',
    'bxslider'
], function (_, $, View, Product, template, Scroll, backend, log) {
    'use strict';

    return View.extend({
        className: 'product-view',
        template: template,
        i18n: true,
        events: {
            'click .back-button': 'back'
        },
        initialize: function () {
            this.$el.css({opacity: 0.0});
        },
        afterRender: function () {
            var images = $('ul.product-view-slider img'),
                i;

            function onLoad() {
                /*jshint validthis:true */
                var containerAspectRatio = $(this).parent().width() / $(this).parent().height(),
                    imageAspectRatio = this.width / this.height;

                if (containerAspectRatio < imageAspectRatio) {
                    $(this).css('width', '100%');
                    $(this).css('height', 'auto');
                }
                else {
                    $(this).css('width', 'auto');
                    $(this).css('height', '100%');
                }
            }

            for (i = 0; i < images.length; i++) {
                images[i].onload = onLoad;
            }
        },
        data : function () {
            return {
                templateId : 1,
                name : 'Билли Бо',
                description : 'Рaзвлeкатeльныйцeнтр «Билли Бо» в Липецке – это сoврeмeнный бoулинг, просторный бильярдный зал со столами для игры в пирамиду и пул, уютный спoрт-бaр оснащенный широкоформатными экранами для просмотра спортивных трансляций. Широкий выбор развлечений для взрослых и детей,корпоративных и индивидуальных клиентов, а именно: 12 дорожек для игры в бoулинг европейского уровня от мирового производителя Brunswick; уютное Sport-кафе для истинных ценителей спорта, в меню которого широкий ассортимент лучших сортов пива, пиццы и других закусок; уникальное видео и аудио оборудование для трансляции спортивных матчей, огромный проекционный монитор; Wi-Fi зона с бесплатным доступом к сети интернет.',
                locations : [
                    {
                        address : 'Адрес: г. Липецк, ул. Титова, д.10 ТРЦ «Москва», 3 этаж',
                        // latitude : 56.118621,
                        // longitude : 40.352206
                    }
                ],
                workTime: 'пн-чт 13:00–01:00; пт. 13:00 – 03:00, сб 11:00–03:00, вс. 11:00 – 01:00',
                phones : [
                    '+7 (4742) 366166'
                ],
                logo : '/images/no-image-logo.png',
                images : [
                    '/images/lipeck/image3.jpeg'
                ],
                site: 'http://www.moskvacenter.ru'
            };
        },
        _open: function (done) {
            this.printAvailable = false;
            this.printDiscountAvailable = false;

            // window.Firefly.trigger('map:locations:product', this.model.get('locations'));

            var that = this,
                $description = this.$('.product-view-description');
            // initialize scroller
            this.scroller = new Scroll($description, $description.find('article'), {
                scrollbar: {
                    $scroll: this.$('.product-view-description-scroll').css('opacity', 0),
                    $scrollKnob: this.$('.product-view-description-scroll-knob')
                },
                options: {
                    scrollingX: false
                }
            });

            // $('.bxslider').bxSlider({
            //     buildPager: function () {
            //         return '<div class="product-view-pager"></div>';
            //     },
            //     onSliderLoad: function () {

            //     }
            // });

            that.$el.css({opacity: 0.0, scale: 2.0}).animate({
                        opacity: 1.0,
                        scale: 1.0
                    }, {
                        complete: done
                    });

        },
        _close: function (done) {
            this.scroller.destroy();
            this.$el.animate({
                opacity: 0.0,
                scale: 2.0
            }, {
                complete: done
            });
        },
        back: function () {
            window.history.back();
        }
    });
});

