/*global define:false*/

define('framework/utils/ticketBackend',[],function () {
    'use strict';

    var application,
        ticketBackend = function (url,path) {
            var base = application.config('ticketBackend');

            if (url === undefined) {
                return base;
            }

            if (url.charAt(0) === '/') {
                url = url.slice(1);
            }
            path = path ? path : '';  
            return base + '/' +path+url;
        };

    ticketBackend.init = function (app) {
        application = app;
    };

    return ticketBackend;
});
