define('views/PreView',[
    'underscore',
    'jquery',
    'framework/View',
    'models/Product',
    'templates/product-view',
    'ftscroller',
    'framework/utils/backend',
    'views/ProductView',
    'bxslider'
], function (_, $, View, Product, template, FTScroller, backend, ProductView) {
    'use strict';
    return ProductView.extend({
        reloadPreview: function (id) {
            this.reloadWithId(id);


            this.afterRender = function () {
                var scrollHeight,
                    knobHeight,
                    images = $('ul.product-view-slider img'),
                    i,
                    span,
                    h1;

                function onLoad() {
                    /*jshint validthis:true */
                    if (this.width >= this.height) {
                        $(this).css('width', '100%');
                        $(this).css('height', 'auto');
                    } else {
                        $(this).css('width', 'auto');
                        $(this).css('height', '100%');
                        $(this).css('margin-left', ($(this).parent().width() - this.width) / 2);
                    }
                }

                for (i = 0; i < images.length; i++) {
                    images[i].onload = onLoad;
                }

                this.$('.bxslider').bxSlider({
                    buildPager: function () {
                        return '<div class="product-view-pager"></div>';
                    }
                });
                this.scroller = new FTScroller(this.$('.product-view-description')[0], {
                    scrollingX: false,
                    scrollbars: false
                });
                this.$scroll = this.$('.product-view-description-scroll');
                this.$scrollKnob = this.$('.product-view-description-scroll-knob');
                scrollHeight = this.$scroll.height();
                knobHeight = this.scroller.scrollHeight ? this.$scroll.height() * this.$scroll.height() / this.scroller.scrollHeight : scrollHeight;
                this.$scrollKnob.css({
                    height: knobHeight
                });

                if (knobHeight >= scrollHeight) {
                    this.$scroll.hide();
                }

                this.scroller.addEventListener('scroll', this.updateScroll);

                // for  Firefox
                if (navigator.userAgent.toLowerCase().indexOf('firefox') > -1) {
                    span = $('span.product-view-contacts-item-heading');
                    span.css('color', '#384B6C');
                    span.css('background-image', 'none');

                    h1 = $('h1.product-view-contacts-heading');
                    h1.css('color', '#384B6C');
                    h1.css('background-image', 'none');
                }

                // for Internet Explorer
                if (navigator.appName === 'Microsoft Internet Explorer') {
                    $('.product-view-contacts-item-heading, .product-view-contacts-heading').css('color', '#384B6C');
                }
            };

            return this.reload();
        }
    });
});
