define('models/Product',[
    'underscore',
    'backbone',
    'framework/utils/backend'
], function (_, Backbone, backend) {
    'use strict';

    return Backbone.Model.extend({
        initialize: function () {
            this.on('sync', function (model) {
                var locations = model.get('locations');

                _.each(locations, function (location) {
                    location.product = model.toJSON();
                });

                model.locations = locations;
            });
        },
        parse: function (response) {
            if (!response.locations) {
                return response;
            }

            _.each(response.locations, function (location) {
                location.product = response;
            });

            return response;
        },
        urlRoot: function () {
            return backend('products');
        }
    });
});
