/*global define:false,setTimeout:false*/

define('framework/utils/async',[],function () {
    'use strict';

    var batchSize = 20,
        interval = 200;

    function nextTick(callback) {
        setTimeout(callback, interval);
    }

    function each(array, iterator, context, callback) {
        var index = 0,
            length = array.length;

        function next() {
            var to = Math.min(index + batchSize, length);

            for (; index < to; index++) {
                iterator.call(context, array[index]);
            }

            if (index < length) {
                nextTick(next);
            } else if (callback) {
                callback();
            }
        }

        nextTick(next);
    }

    function parallel (tasks, callback) {
        var pending = tasks.length;

        function done () {
            if (! -- pending) callback && callback();
        }

        if (! pending) return callback && callback();

        tasks.forEach(function (task) {
            task(done)
        });
    }

    /**
     * Executes taks in series
     * @param  {Array}   tasks
     * @param  {Function} callback
     */
    function serial(tasks, callback) {
        (function next(){
            tasks.length ? tasks.shift()(next) : callback();
        }());
    }



     /**
     * Object that acts like mutex, effectively serializing async operations
     */
    function Mutex () {
        this._locked = false;
        this._queue = [];
    }

    Mutex.prototype.lock = function (operation) {
        if (!this._locked) {
            this._locked = true;
            if (operation) {
                operation();
            }
        } else if (operation) {
            this._queue.unshift(operation);
        }

        return this;
    };

    Mutex.prototype.unlock = function () {
        if (!this._locked) {
            return false;
        }

        var operation = this._queue.shift();

        if (operation) {
            operation();
        } else {
            this._locked = false;
        }

        return this;
    };

    Mutex.prototype.locked = function () {
            return this._locked;
    };

    Mutex.prototype.unlocked = function () {
        return !this._locked;
    };

    return {
        each: each,
        Mutex: Mutex,
        parallel: parallel,
        serial: serial
    };
});
