/*global define:false*/

define('views/CurrencyWidgetView',[
    'framework/View',
    'templates/currency-widget-view'
], function (View, template) {
    'use strict';

    return View.extend({
        className: 'currency-widget-view',
        template: template,
        i18n: true,
        initialize: function () {
            this.$el.css('opacity', 0.0);
        },
        data: function () {
            return [
                {
                    "name": "dollar",
                    "sign": "$",
                    "buy": "65,13",
                    "sell": "65,71"
                },
                {
                    "name": "euro",
                    "sign": "€",
                    "buy": "69,10",
                    "sell": "70,35"
                }
            ];
        },
        _open: function (done) {
            this.$el.animate({
                opacity: 1.0
            }, {
                complete: done
            });
        },
        _close: function (done) {
            this.$el.animate({
                opacity: 0.0
            }, {
                complete: done
            });
        }
    });
});

