define('views/HostelView',[
    'underscore',
    'jquery',
    'framework/View',
    'templates/hotel-view',
    'framework/utils/Scroll',
    'framework/utils/backend',
    'framework/utils/log',

    'views/SearchView',
    'collections/_eventCollection',
    'models/ticketCart',
    'views/TicketViewTable',
    'views/MessageView'
], function (_, $, View, template, Scroll, backend, log, SearchView, _eventCollection, ticketCart, TicketViewTable, MessageView) {
    'use strict';

    var months = ['Январь', 'Февраль', 'Март', 'Апрель', 'Май', 'Июнь', 'Июль', 'Август', 'Сентябрь', 'Октябрь', 'Ноябрь', 'Декабрь'];

    return View.extend({
        className: 'hotel-view',
        template: template,
        events: {
            'click .back-button': 'back',
            'click button.ticket-view-buy-button': 'proceed',
            'click .ticket-view-table-choice-minus': 'clickMinus',
            'click .ticket-view-table-choice-plus': 'clickPlus',
            'click .hotel-view-date-section input': 'activateCalendar',
            'click .hotel-view-calendar-done': 'clickDone'
        },
        initialize: function (options) {
            options = options || {};
            this.$el.css({opacity: 0.0, scale: 2.0});
            _.bindAll(this, 'clickDone');

            this.eventId = options.eventId;
            this.deltaY = 18; // delta to scroll numbers
            this.preventScroll = 0;
        },
        afterRender: function () {
            var date = new Date(),
                today = date.getDate(),
                month = date.getMonth(),
                day,
                i,
                $dateContainer = this.$('.hotel-view-calendar-body'),
                self = this,
                $div,
                from, to, $today;

            function chooseDate ($evt) {
                $evt.stopPropagation();
                var $el = $($evt.fromElement),
                    position = $el.position(),
                    day = +$el.attr('data-day');
                    console.log(position);
                if (self.$activeCircle === self.$fromCircle) {
                    from = day;
                } else {

                    if (day < from) {

                        return;
                    }

                    to = day;
                }
                var scaleCorrection = 1;// / (/scale\((\d*.\d*)/.exec($('.viewport.container').attr('style')))[1];
                self.$activeCircle.animate({
                    marginTop: position.top * scaleCorrection - 60,
                    marginLeft: position.left * scaleCorrection + 3
                }, 400);

                self.$activeInput.val($($evt.fromElement).attr('data-day') + '.' + date.getMonth() + '.' + date.getFullYear());
            }

            this.$('.hotel-view-date-from').val(date.getDate() + '.' + (date.getMonth() + 1) + '.' + date.getFullYear());
            date.setDate(date.getDate() + 1);
            this.$('.hotel-view-date-to').val(date.getDate() + '.' + (date.getMonth() + 1) + '.' + date.getFullYear());
            date.setDate(1);
            day = date.getDate();

            // filling empty days before month starts
            for (i = date.getDay(); i > 1; i--) {
                $dateContainer.append($('<div class="no-date">0</div>'));
            }

            while (date.getMonth() === month) {
                $div = $('<div>');
                if (date.getDay() === 0 || date.getDay() === 6) {
                    $div.addClass('weekend');
                }

                if (date.getDate() === today) {
                    $today = $div.addClass('today');
                }

                $dateContainer.append($div.html(day).attr('data-day', day));
                date.setDate(++day);
            }

            this.$('.hotel-view-calendar-header-month').html(months[date.getMonth()]);
            this.$('.hotel-view-calendar-header-year').html(date.getFullYear());

            this.$toCircle = this.$('.hotel-view-calendar-to');
            this.$fromCircle = this.$('.hotel-view-calendar-from');
            this.$calendar = this.$('.hotel-view-calendar')
                .on('click', 'div', chooseDate)
                .css('opacity', 0);


        },
        activateCalendar: function ($evt) {

            if (this.$activeInput) {
                this.$activeInput.removeClass('active');
                this.$activeCircle.removeClass('active');
            }

            var $input = this.$activeInput = $($evt.fromElement).addClass('active');
            this.$calendar.addClass('activated');
            if (/from/.test($input.attr('class'))) {
                this.$activeCircle = this.$fromCircle;
            } else {
                this.$activeCircle = this.$toCircle;
            }
            this.$activeCircle.addClass('active');


        },
        clickDone: function () {

            this.deactivateCalendar();
        },
        deactivateCalendar: function () {
            this.$calendar.removeClass('activated');
            this.$activeInput.removeClass('active');
            this.$activeCircle.removeClass('active');
        },
        clickMinus: function (evt) {
            var $parent = $(evt.fromElement).parent();

            (function (curAmount, $el, dy) {
                if (curAmount !== 0) {
                    var $curEl = $el.find('span[data-active]');

                    if ($curEl.prev().length === 0) {
                        $el.prepend('<span style="margin-top:-18px"> ' + (curAmount - 1));
                    }

                    setTimeout(function () {
                        $el.find('span').first().css('margin', 0);
                    }, 0);

                    $curEl.removeAttr('data-active');
                    $curEl.prev().attr('data-active', curAmount - 1);
                    $el.css('transform', 'translateY(' + ($curEl.prevAll().length - 1) * (-dy) + 'px)');

                }
            })(+$parent.find('span[data-active]').attr('data-active'),
                $parent.find('.numbers-container'),
                18);
        },
        clickPlus: function (evt) {
            var $parent = $(evt.fromElement).parent();


            (function (curAmount, $el, dy) {

                if (curAmount >= $el.find('span').attr('data-max')) {
                    return;
                }
                var $curEl = $el.find('span[data-active]');
                if ($curEl.next().length === 0) {
                    $el.append('<span>' + (curAmount + 1));
                }
                $curEl.removeAttr('data-active');
                $curEl.next().attr('data-active', curAmount + 1);
                $el.css('transform', 'translateY(' + ($curEl.prevAll().length + 1) * (-dy) + 'px)');
                if ($curEl.next().length === 0) {
                    $el.append('<span>' + (curAmount + 1));
                }

            })(+$parent.find('span[data-active]').attr('data-active'),
                $parent.find('.numbers-container'),
                18);

        },
        data: function () {

            return {
                name: 'Хостел Like Липецк',
                address: 'г. Липецк, ул. Советская, д.66., корпус Б, 3 этаж',
                phone: ' +7 (904) 290 2444',
                site: 'http://lipetsk.likehostels.ru/',
                description: 'Много лет активные путешественники в различных уголках мира предпочитают останавливаться в таких заведениях, как частная мини-гостиница. Ведь это не только наиболее экономичное размещение в любом городе, но также и прекрасная возможность познакомиться с разными интересными людьми. Ранее такие гостиницы, как “Лайк Хостел Липецк” считались предназначенными исключительно для молодежи, однако теперь бюджетный комфортный отдых привлекает людей разного возраста и положения. Сегодня среди гостей, которых привлекает мини-гостиница хостел, можно встретить, например, профессора, который с удовольствием готовит совместный ужин на общей кухне со студентами вместо похода в ресторан.',
                logo1: '/images/no-image.png',
                logo2: '/images/lipeck/image12.jpeg',
                numbers: [{
                    category: 'Одноместный',
                    amount: '23',
                    price: 500
                }, {
                    category: 'Двухместный',
                    amount: '40',
                    price: 700
                }]
            };
        },

        _open: function (done) {
            var $description = this.$('.ticket-view-description'),
                self = this;
            this.scroller = new Scroll($description, $description.find('p'), {
                scrollbar: {
                    $scroll: this.$('.ticket-view-description-scroll').css('opacity', 0),
                    $scrollKnob: this.$('.ticket-view-description-scroll-knob')
                },
                callback: function (left, top) {

                    if (top > 0) {
                        $description.css('box-shadow', 'inset 0px 17px 14px -17px');
                    } else {
                        $description.css('box-shadow', 'none');
                    }

                },
                options: {
                    scrollingX: false
                }
            });


            this.scrollTable = new Scroll($('.ticket-view-table-wrapper'), $('.ticket-view-table-wrapper table'), {
                scrollbar: {
                    $scroll: this.$('.ticket-view-table-scroll').css('opacity', 0),
                    $scrollKnob: this.$('.ticket-view-table-scroll-knob')
                },
                options: {
                    scrollingX: false
                }
            });

            this.$el.animate({
                opacity: 1.0,
                scale: 1
            }, {
                complete: function () {
                    var $today = $('.today'),
                        position = $today.position(),
                        scaleCorrection = 1;// / (/scale\((\d*.\d*)/.exec($('.viewport.container').attr('style')))[1];
                    position = $('.today').position();

                    self.$fromCircle.css({
                        marginLeft: position.left * scaleCorrection + 3,
                        marginTop: position.top *scaleCorrection - 60
                    });

                    position = $today.next().position();

                    self.$toCircle.css({
                        marginLeft: position.left * scaleCorrection + 3,
                        marginTop: position.top * scaleCorrection - 60
                    });

                    self.$calendar.animate({scale: 0.8}, {
                        duration: 0,
                        complete: function () {
                            self.$calendar.css('opacity', 1);
                        }
                    });
                    done();
                }
            });

        },
        _close: function (done) {
            this.scroller.destroy();
            this.scrollTable.destroy();
            this.$el.animate({
                opacity: 0.0,
                scale: 2
            }, {
                complete: done
            });

        },
        back: function () {
            window.history.back();
        },
        proceed: function () {
            Firefly.viewport.showMessage(new MessageView({
                message: 'Места забронированы'
            }));
        }
    });
});

